import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {QUICK_EDIT_CLICKED} from '../../constants';
import {t} from 'sn-translate';
import debounce from 'lodash/debounce';
import isEmpty from 'lodash/isEmpty';

const tooltipOffset = 8; // pixels
const tooltipDelay = 100; // milliseconds

const showTooltip = (evt, tooltipPosition) => {
	const btn = evt.currentTarget;
	return debounce((btn, tooltipPosition) => {
		const btnDim = btn.getBoundingClientRect();

		const tooltip = btn.nextSibling;
		tooltip.style.display = 'block';
		tooltip.setAttribute('aria-hidden', false);

		if (isEmpty(tooltipPosition)) {
			const tooltipDim = tooltip.getBoundingClientRect();
			tooltipPosition.width = tooltipDim.width;
			tooltipPosition.height = tooltipDim.height;
		}

		tooltipPosition.x =
			btnDim.x -
			parseInt(tooltipPosition.width / 2, 10) +
			parseInt(btnDim.width / 2, 10);
		tooltipPosition.y =
			btnDim.y - parseInt(tooltipPosition.height, 10) - tooltipOffset;

		tooltip.style.top = tooltipPosition.y + 'px';
		tooltip.style.left = tooltipPosition.x + 'px';
		tooltip.style.visibility = 'visible';
	}, tooltipDelay)(btn, tooltipPosition);
};

const hideTooltip = evt => {
	const btn = evt.currentTarget;
	const tooltip = btn.nextSibling;

	return debounce(tooltip => {
		tooltip.style.display = 'none';
		tooltip.style.visibility = 'hidden';
		tooltip.setAttribute('aria-hidden', true);
	}, tooltipDelay)(tooltip);
};

export const renderQuickEdit = ({dispatch, rowDisplayValue, rowSysId}) => {
	const ariaLabel = t('Open Preview Of {0}', rowDisplayValue);
	const ttLabel = t('Open Preview');
	const tooltipPosition = {};

	const clickHandler = evt => {
		evt.target.closest('tr').classList.add('quick-edit-highlight');
		dispatch(QUICK_EDIT_CLICKED, {
			quickEditSysId: rowSysId
		});
	};

	return (
		<Fragment>
			<button
				aria-label={ariaLabel}
				className="quick-edit-button"
				on-click={evt => clickHandler(evt)}
				// on-mouseenter={evt => {
				// 	showTooltip(evt, tooltipPosition);
				// }}
				// on-mouseleave={hideTooltip}
				// on-focus={evt => {
				// 	showTooltip(evt, tooltipPosition);
				// }}
				// on-blur={hideTooltip}
			>
				<span className="info-icon" aria-hidden="true">
					<now-icon icon="circle-info-outline" size="sm"></now-icon>
				</span>
			</button>

			{/* <div
				className="now-tooltip sn-grid-quickedit-cell-tooltip"
				aria-hidden="true">
				<span className="now-line-height-crop">{ttLabel}</span>
			</div> */}
		</Fragment>
	);
};
