import DeclarativeActionsButton from './buttonView';
import DeclarativeActionsSplitButton from './splitButtonView';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

const DeclarativeActionsWrapper = state => {
	const {properties, transformedActions} = state;
	const {count, model} = properties;
	const commonProps = {
		count,
		model
	};

	if (isEmpty(transformedActions)) return <div />;

	const getButton = action => {
		return (
			<DeclarativeActionsButton
				{...commonProps}
				action={action}
				variant={action.buttonType}
			/>
		);
	};

	const getSplitButton = (grouped, index) => {
		return grouped.groupedActions.length === 1 ? (
			getButton(grouped.groupedActions[0], `single_grouped_${index}`)
		) : (
			<DeclarativeActionsSplitButton
				{...commonProps}
				actions={grouped.groupedActions}
			/>
		);
	};

	return (
		<div className="declarative-actions-wrapper">
			{transformedActions.map((action, index) =>
				get(action, 'groupId', '')
					? getSplitButton(action, index)
					: getButton(action, `single_${index}`)
			)}
		</div>
	);
};

export default DeclarativeActionsWrapper;
