import get from 'lodash/get';
import set from 'lodash/set';

export const PLUGIN_NAME = 'featureFlags';
export const BEHAVIOR_PATH = ['behaviors', PLUGIN_NAME];
export const OPTIONS_PATH = ['options', PLUGIN_NAME];

const getFeatureFlags = state =>
	get(state, [...OPTIONS_PATH, 'featureFlags'], {});

const pluginGetter = (state, path, dflt) =>
	get(state, [...OPTIONS_PATH, path, dflt]);

export const featureFlagSelectors = {
	getFeatureFlags
};

export function instantiatePlugin(options) {
	return {
		transform(state) {
			const {
				properties: {
					hideCellFilter,
					hideColumnSorting,
					hideColumnGrouping,
					hideColumnFiltering,
					hideColumnResizing,
					hideLinks,
					hideLiveList,
					hideHighlightContent,
					hideHighlightedValues,
					hideSelectAll
				}
			} = state;

			const headerCellFlags = {
				hideColumnSorting,
				hideColumnGrouping,
				hideColumnFiltering,
				hideColumnResizing,
				hideSelectAll
			};

			const bodyCellFlags = {
				hideCellFilter,
				hideLinks,
				hideLiveList,
				hideHighlightContent,
				hideHighlightedValues
			};

			const featureFlags = {
				headerCellFlags,
				bodyCellFlags
			};

			return {
				...state,
				options: {
					...state.options,
					featureFlags: {
						featureFlags
					}
				}
			};
		},
		behavior: {
			name: PLUGIN_NAME
		}
	};
}
