import '@servicenow/now-condition-builder';
import '@servicenow/now-modal';
import {
	ADVANCED_VIEW_SET_QUERY,
	CANCEL_BUTTON_ID,
	CANCEL_LABEL,
	CLOSE_MODAL,
	CONDITION_BUILDER_ENCODED_QUERY_SET,
	LIST_ADVANCED_VIEW_FIXED_QUERY_SET,
	MODAL_ACTIONS,
	QUERY_DELIMITER,
	UPDATE_BUTTON_ID,
	UPDATE_LABEL
} from '../../constants';
import {Logger} from '../../../src/utils/logger';
import {createCustomElement} from '@seismic/core';
import {t} from 'sn-translate';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import snabbdom from '@seismic/snabbdom-renderer';
import styles from './styles.scss';

const LOG = Logger().createLog('List Advanced View Modal');

const advancedViewModalTitle = t('Advanced View');

const removeDelimiter = (query = '', startsWith = true) => {
	if (startsWith) {
		if (!query.startsWith(QUERY_DELIMITER)) return query;

		return query.replace(QUERY_DELIMITER, '');
	} else {
		if (!query.endsWith(QUERY_DELIMITER)) return query;

		return query.slice(0, query.length - 1);
	}
};

const getQuery = ({
	dispatch,
	fixedQuery = {},
	query = '',
	relatedListName = ''
}) => {
	if (isEmpty(relatedListName)) return query;

	const fixedQueryValue = removeDelimiter(
		get(fixedQuery, relatedListName, ''),
		false
	);

	if (query.indexOf(fixedQueryValue) > -1) {
		dispatch(LIST_ADVANCED_VIEW_FIXED_QUERY_SET, {
			value: fixedQueryValue
		});
		return removeDelimiter(query.replace(fixedQueryValue, ''));
	}

	return query;
};

const footerActionClickedEffect = coeffects => {
	const {
		action,
		state: {conditionBuilderFormDirty, encodedQuery, fixedQueryValue},
		dispatch
	} = coeffects;

	const id = get(action, 'payload.action.id', '');

	if (id === UPDATE_BUTTON_ID && conditionBuilderFormDirty) {
		const conditions = isEmpty(fixedQueryValue)
			? encodedQuery
			: fixedQueryValue + QUERY_DELIMITER + encodedQuery;

		dispatch(ADVANCED_VIEW_SET_QUERY, {
			query: conditions
		});
	}
	dispatch(CLOSE_MODAL);
};

const view = (state, {dispatch}) => {
	const {
		properties: {fixedQuery, listTitle, query, relatedListName, table}
	} = state;
	const encodedListQuery = getQuery({
		dispatch,
		fixedQuery,
		query,
		relatedListName
	});
	const footerButtons = [
		{
			label: UPDATE_LABEL,
			variant: 'primary',
			tooltipContent: UPDATE_LABEL,
			id: UPDATE_BUTTON_ID,
			configAria: {'aria-label': UPDATE_LABEL}
		},
		{
			label: CANCEL_LABEL,
			variant: 'secondary',
			tooltipContent: CANCEL_LABEL,
			id: CANCEL_BUTTON_ID,
			configAria: {'aria-label': CANCEL_LABEL}
		}
	];

	return (
		<now-modal
			size="lg"
			header-label={advancedViewModalTitle}
			manage-opened
			opened={true}
			footerActions={footerButtons}>
			<div className="condition-builder-wrapper">
				<now-condition-builder
					tableName={table}
					tableLabel={listTitle}
					encodedQuery={encodedListQuery}
					hideRelatedListQueryConditions={true}
					legacy={true}
				/>
			</div>
		</now-modal>
	);
};

createCustomElement('sn-record-list-modal-advanced-view', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		encodedQuery: '',
		fixedQueryValue: '',
		conditionBuilderFormDirty: false
	},
	properties: {
		encodedListQuery: {
			default: ''
		},
		fixedQuery: {
			default: {}
		},
		listTitle: {
			default: ''
		},
		relatedListName: {
			default: ''
		},
		table: {
			default: ''
		},
		query: {
			default: ''
		}
	},
	actionHandlers: {
		[MODAL_ACTIONS.FOOTER_ACTION_CLICKED]: {
			effect: footerActionClickedEffect,
			stopPropagation: true
		},
		[MODAL_ACTIONS.OPENED_SET]: {
			effect: footerActionClickedEffect,
			stopPropagation: true
		},
		[CONDITION_BUILDER_ENCODED_QUERY_SET]: {
			effect: ({action, updateState}) => {
				updateState({
					encodedQuery: action.payload.value,
					conditionBuilderFormDirty: true
				});
			},
			stopPropagation: true
		},
		[LIST_ADVANCED_VIEW_FIXED_QUERY_SET]: {
			effect: ({action, updateState}) => {
				updateState({
					fixedQueryValue: action.payload.value
				});
			},
			stopPropagation: true
		}
	},
	styles
});
