import * as recordDataPlugin from '../recordDataTransform/recordDataTransform';
import {
	NOW_GRID_ROWS_REORDERED,
	NOW_GRID_TREE_RENDERED,
	NOW_SORTABLE_UPDATE_BEHAVIOR
} from '../constants';
import nowSortable from '@devsnc/library-vtb-commons/lib/sortable/behavior';

import get from 'lodash/get';
import isFunction from 'lodash/isFunction';
import set from 'lodash/set';

import {applyStyle, renderTemplate} from '../utils/utils';

export const PLUGIN_NAME = nowSortable.name;
export const ACTIONHANDLER_PATH = ['actionHandlers', PLUGIN_NAME];
export const OPTIONS_PATH = ['options', PLUGIN_NAME];
export const TEMPLATE_PATH = 'templates';
export const NOW_GRID_SORT_END = 'NOW_GRID_SORT_END';

/* Getter for the "dropZone property" */
const getDropZone = state => get(state, [...OPTIONS_PATH, 'dropZone'], '');

/* This method is used to update the default drag-drop configuration based on properties to now-record-list */
const dynamicConfig = host => {
	const {
		hideDragDrop,
		yScrollParent,
		additionalDropZones,
		customSourcePlaceholder,
		customDestPlaceholder,
		cloneAppendTo,
		customClone
	} = host;

	const options = {};

	options['dragSupport'] = !hideDragDrop;

	options['keyboardSupport'] = !hideDragDrop;

	if (yScrollParent) options['yScrollParent'] = `.${yScrollParent}`;

	if (additionalDropZones) options['connectedLists'] = additionalDropZones;

	if (cloneAppendTo) options['cloneAppendTo'] = cloneAppendTo;

	if (isFunction(customSourcePlaceholder))
		options['customSourcePlaceholder'] = customSourcePlaceholder;

	if (isFunction(customDestPlaceholder))
		options['customDestPlaceholder'] = customDestPlaceholder;

	if (isFunction(customClone)) options['clone'] = customClone;

	return options;
};

/**
 * The following can be customized:
 * 1. The source placeholder (i.e. the markup displayed in place of the item being dragged) by configuring customSourcePlaceholder.
 * 2. The destination placeholder (i.e. the markup displayed to indicate an area where the item can be dropped) by configuring customDestPlaceholder.
 * 3. The clone (i.e. representational markup to indicate the item being dragged).
 */

const customSourcePlaceholder = srcEl => {
	var tr = document.createElement('tr');
	tr.innerHTML = srcEl.innerHTML;
	tr.classList.add('now-grid-src-placeholder');
	return tr;
};

const customDestPlaceholder = srcEl => {
	var tr = document.createElement('tr');
	tr.innerHTML = srcEl.innerHTML;
	tr.childNodes.forEach(cell => {
		cell.innerHTML = '';
	});
	tr.classList.add('now-grid-dest-placeholder');
	return tr;
};

const customClone = el => {
	var tr = document.createElement('tr');
	tr.classList.add('now-grid-drag-clone');

	let firstSet = false;
	el.childNodes.forEach(cell => {
		let td = document.createElement('td');
		let text = cell.title ? cell.title : cell.innerText;
		td.innerHTML = text;
		if (!firstSet && cell.innerText) {
			firstSet = true;
			td.classList.add('visible');
		} else {
			td.classList.add('hidden');
		}
		td.style.width = `${cell.offsetWidth}px`;
		td.style.height = `${cell.offsetHeight}px`;
		tr.appendChild(td);
	});
	return tr;
};

/* The default set of options being passed to the now-sortable behaviour */
export const defaultOptions = {
	items: '.row',
	dropzone: '.table-body',
	actionPrefix: 'NOW_GRID_',
	listOrder: 'vertical',
	yScrollParent: '.now-grid',
	cloneAppendTo: '.now-grid',
	transitions: true,
	customSourcePlaceholder,
	customDestPlaceholder,
	clone: customClone,
	keyboardSupport: true,
	dragSupport: true,
	connectedLists: '',
	lockAxis: 'y',
	dynamicConfig
};

/* The default drop-handler to be shipped with this plugin */
export const dropHandler = {
	effect: ({state, action, dispatch, updateState}) => {
		const {
			payload: {sourceIndex, destinationIndex}
		} = action;
		if (sourceIndex === destinationIndex) return;

		const newRows = [...recordDataPlugin.selectors.getDataFromBehavior(state)];
		const movedItem = newRows[sourceIndex];
		// Remove item from source index
		newRows.splice(sourceIndex, 1);
		// Add it at the destination index
		newRows.splice(destinationIndex, 0, movedItem);
		const updatedData = {...get(state, recordDataPlugin.BEHAVIOR_PATH)};
		updatedData.data = newRows;
		updateState({
			path: `behaviors.${recordDataPlugin.PLUGIN_NAME}`,
			value: updatedData,
			operation: 'set'
		});

		// Dispatch to parent
		dispatch(NOW_GRID_ROWS_REORDERED, {sourceIndex, destinationIndex});
		// TODO: The server may not be able to make the update in sync with the client side update state
		// Need to handle errors on the state - on the handler
	}
};

/* The following method is used to re-trigger/refresh the now-sortable initialization */
export const refreshDragDrop = ({dispatch}) => {
	dispatch(NOW_SORTABLE_UPDATE_BEHAVIOR);
};

/* The default template changes needed for the drag-drop plugin to work */
export const defaultTemplates = {
	body: context => {
		const {templates, rowData, properties} = context;
		const dropZone = getDropZone(properties);
		return (
			<tbody
				className={`table-body ${dropZone} `}
				{...applyStyle(properties.style, context)}>
				{rowData.map((row, rowIndex) =>
					renderTemplate('bodyRow', templates, {
						...context,
						row,
						rowIndex
					})
				)}
			</tbody>
		);
	},

	bodyRow: context => {
		const {templates, columns, properties} = context;
		return (
			<tr
				tabIndex="0"
				className="row"
				{...applyStyle(properties.style, context, {
					style: {display: 'table-row'}
				})}>
				{columns.map((column, columnIndex) =>
					renderTemplate('bodyCell', templates, {
						...context,
						column,
						columnIndex
					})
				)}
			</tr>
		);
	}
};

export function instantiatePlugin() {
	return {
		transform(state) {
			const {
				properties: {dropZone, additionalDropZones}
			} = state;
			set(state, [...OPTIONS_PATH, 'dropZone'], dropZone);
			set(state, [...OPTIONS_PATH, 'additionalDropZones'], additionalDropZones);
			return state;
		},

		behavior: {
			name: nowSortable.name,
			actionHandlers: {
				...nowSortable.actionHandlers,
				[NOW_GRID_TREE_RENDERED]: refreshDragDrop,
				[NOW_GRID_SORT_END]: dropHandler
			}
		},
		options: defaultOptions
	};
}
