import {
	CELL_FILTERING_ENTER,
	CELL_FILTERING_TAB,
	KEY_ARROW_DOWN,
	KEY_ARROW_UP,
	KEY_ENTER,
	KEY_SPACEBAR,
	KEY_TAB
} from '../constants';
import keyBindingBehavior from '@servicenow/behavior-key-binding';

export const cellFilterKeyBindingBehavior = {
	behavior: keyBindingBehavior,
	options: {
		keyBindings: {
			onArrowKey: [
				KEY_ARROW_UP,
				KEY_ARROW_DOWN,
				KEY_ENTER,
				KEY_SPACEBAR,
				KEY_TAB
			]
		},
		handlers: {
			onArrowKey: function(host, event, dispatch) {
				const cellFilterId = host.shadowRoot.activeElement.getAttribute(
					'aria-activedescendant'
				);
				let cellFilterIndex = Number(
					cellFilterId.charAt(cellFilterId.length - 1)
				);

				if (event.key === KEY_ARROW_UP || event.key === KEY_ARROW_DOWN) {
					cellFilterIndex === 0
						? (cellFilterIndex += 1)
						: (cellFilterIndex -= 1);
					event.preventDefault();
					dispatch('PROPERTIES_SET', {
						selectionIndex: cellFilterIndex
					});
				} else if (event.key === KEY_TAB) {
					dispatch(CELL_FILTERING_TAB);
				} else if (event.key === KEY_ENTER || event.key === KEY_SPACEBAR) {
					dispatch(CELL_FILTERING_ENTER);
				}
			}
		}
	}
};
