import '../filterPanelButton/filterPanelButton';
import '../listCopyUrlButton/listCopyUrlButton';
import '../listRefresh/listRefresh';
import '@servicenow/now-dropdown';
import '@servicenow/now-toggle';
import * as ListHeaderToolbarService from './listHeaderToolbarService';
import {
	DROPDOWN_ITEM_CLICKED,
	INVALID_ACTION_ITEM,
	LIST_TYPES,
	NOW_LIST_USE_NOW_GRID,
	NOW_TOGGLE_CHECKED_SET,
	SIZE_MEDIUM,
	SIZE_SMALL
} from '../../constants';
import {addRetainedElement} from '../../utils/focusUtility';
import {createCustomElement} from '@servicenow/ui-core';
import {
	dirtyBehavior,
	dirtyModalInterceptor
} from '../../behaviors/dirtyBehavior/dirtyBehavior';
import {isEdge, isIE11} from '../../utils/browser';
import {t} from 'sn-translate';
import get from 'lodash/get';
import isUndefined from 'lodash/isUndefined';
import snabbdom, {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './listHeaderToolbar.scss';

const LIST_ACTIONS = 'List Actions';

const view = ({properties}, {dispatch}) => {
	const {
		children,
		headerToolbarProps,
		hideColumnResizing,
		hideFilterPanel,
		hideMenuButton,
		hidePanel,
		hideRefreshButton,
		hideListSharing,
		hideLiveList,
		isWorkspace,
		listTitle,
		listType,
		liveListCount,
		liveLists,
		menuSelection,
		panelOpened,
		panelConfig,
		parsedQueryCount,
		size
	} = properties;

	const isIE11orEdge = isIE11() || isEdge();
	const dropdownClass = isIE11orEdge
		? 'sn-list-header-toolbar-button-wrapper ie11edge-alignment'
		: 'sn-list-header-toolbar-button-wrapper';

	const overrideHasDropdown =
		listType !== LIST_TYPES.DEFAULT && !hideColumnResizing;

	const hasDropdown =
		(!hideMenuButton &&
			ListHeaderToolbarService.isTableSupported(
				menuSelection,
				hideColumnResizing
			)) ||
		overrideHasDropdown;
	const hasPanel = !hideFilterPanel && !hidePanel;
	const buttonSize = size === SIZE_SMALL ? size : SIZE_MEDIUM;
	const buttonVariant = size === SIZE_SMALL ? 'tertiary' : 'secondary';
	const {panelType} = panelConfig;

	return (
		<div className="sn-list-header-toolbar">
			{children}

			{!hideRefreshButton ? (
				<sn-record-list-header-toolbar-button-refresh
					triggerRefresh={headerToolbarProps.triggerRefresh}
					size={buttonSize}
					variant={buttonVariant}
					liveListCount={liveListCount}
					liveLists={liveLists}
					listTitle={listTitle}
					hideLiveList={hideLiveList}
					panelType={panelType}
				/>
			) : null}

			{hasDropdown ? (
				<Fragment>
					<div className={dropdownClass}>
						{renderDropdown({
							properties,
							buttonSize,
							buttonVariant,
							overrideHasDropdown,
							listTitle
						})}
					</div>
				</Fragment>
			) : null}

			{!hideListSharing ? (
				<sn-record-list-header-toolbar-button-copy-url
					size={buttonSize}
					variant={buttonVariant}
					listTitle={listTitle}
				/>
			) : null}

			{hasPanel ? (
				<sn-record-list-header-toolbar-button-filter-panel
					count={parseInt(parsedQueryCount, 10)}
					size={buttonSize}
					variant={buttonVariant}
					panelConfig={panelConfig}
					panelOpened={panelOpened}
					listTitle={listTitle}
				/>
			) : null}
		</div>
	);
};

const renderDropdown = ({
	properties,
	buttonSize,
	buttonVariant,
	overrideHasDropdown,
	listTitle
}) => {
	const {menuSelection, hideColumnResizing} = properties;
	const dropdownOptions = ListHeaderToolbarService.getOptions({
		menuSelection,
		hideColumnResizing,
		overrideHasDropdown
	}).map((item, index) => ({
		id: index,
		label: item.title
	}));
	const disabledAttr = isUndefined(dropdownOptions) ? {disabled: true} : {};
	const dropdownAriaLabel = t(LIST_ACTIONS + ' for ' + listTitle);

	return dropdownOptions.length ? (
		<div
			on-click={event => addRetainedElement('listActionsButton', event.target)}>
			<now-dropdown
				icon="gear-outline"
				size={buttonSize}
				variant={buttonVariant}
				select="none"
				tooltip-content={LIST_ACTIONS}
				hide-caret
				items={dropdownOptions}
				config-aria={{
					trigger: {'aria-label': dropdownAriaLabel}
				}}
				{...disabledAttr}
			/>
		</div>
	) : null;
};

createCustomElement('sn-record-list-header-toolbar', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		columns: {default: ''},
		headerToolbarProps: {default: {}},
		hideColumnResizing: {default: {}},
		hideFilterPanel: {default: {}},
		hideMenuButton: {default: {}},
		hidePanel: {default: {}},
		hideRefreshButton: {default: {}},
		hideListSharing: {default: false},
		hideLiveList: {default: true},
		isTableEmpty: {default: false},
		isWorkspace: {default: true},
		listTitle: {default: ''},
		listType: {default: {}},
		menuSelection: {default: ''},
		panelConfig: {default: {}},
		panelOpened: {default: false},
		parsedQueryCount: {default: {}},
		query: {default: ''},
		recordCount: {default: 0},
		relatedListName: {default: ''},
		selectedListId: {default: ''},
		size: {default: SIZE_MEDIUM},
		table: {default: ''},
		userPreferences: {default: []},
		liveListCount: {default: 0},
		liveLists: {default: false}
	},
	styles: styles,
	actionHandlers: {
		[DROPDOWN_ITEM_CLICKED]: {
			effect: ({dispatch, action: {payload}, state}) => {
				const {overrideHasDropdown} = state;
				const {menuSelection, hideColumnResizing} = state.properties;
				const {
					item: {id}
				} = payload;

				const dropdownOptions = ListHeaderToolbarService.getOptions({
					menuSelection,
					hideColumnResizing,
					overrideHasDropdown
				});
				const option = get(dropdownOptions, id, {
					value: INVALID_ACTION_ITEM
				});
				option.action({dispatch, ...state.properties});
			},
			stopPropagation: true,
			interceptors: [dirtyModalInterceptor]
		},
		[NOW_TOGGLE_CHECKED_SET]: {
			effect: ({
				dispatch,
				action: {
					payload: {value}
				}
			}) => {
				dispatch(NOW_LIST_USE_NOW_GRID, {value});
			}
		}
	},
	behaviors: [dirtyBehavior]
});
