import {Logger} from '../../../utils/logger';
import {SPLIT_BUTTON_CLICKED, SPLIT_BUTTON_ITEM} from '../../../constants';
import {dirtyModalInterceptor} from '../../../behaviors/dirtyBehavior/dirtyBehavior';
import {dispatchToMediator} from '../helpers';
import find from 'lodash/find';
import isEmpty from 'lodash/isEmpty';

const LOG = Logger().createLog('SplitButtonActions');

export default {
	[SPLIT_BUTTON_CLICKED]: {
		effect: ({dispatch, action: {payload}}) => {
			const {actions, model} = payload;
			dispatchToMediator({action: actions[0]}, {model}, dispatch);
		},
		stopPropagation: true,
		interceptors: [dirtyModalInterceptor]
	},
	[SPLIT_BUTTON_ITEM]: {
		effect: ({dispatch, action: {payload}}) => {
			const {actions, model, item} = payload;
			const actionFound = find(actions, action => action.name === item.id);

			if (isEmpty(actionFound)) {
				LOG.error('Action not found for item.id: ', item.id);
			} else {
				dispatchToMediator({action: actionFound}, {model}, dispatch);
			}
		},
		stopPropagation: true,
		interceptors: [dirtyModalInterceptor]
	}
};
