import * as _ from 'lodash';

// Build a dynamic style.
//
// fnOrStyle is either a function that will return an object or it's a string `className`.
//
// The result of this function should be applied to a tag with a spread.
//
// If fnOrStyle is a function, context and defaultStyle are passed in and can be used
// or extended as desired.
export function applyStyle(fnOrStyle, context = undefined, defaultStyle = {}) {
	if (!fnOrStyle) {
		return defaultStyle;
	}

	// If it's a function, call it with the context
	if (_.isFunction(fnOrStyle)) {
		fnOrStyle = fnOrStyle(context, defaultStyle);
	}

	// If it's a string, append the class
	// NOTE: Although this "works", since we are using Shadow DOM, only the names of styles defined
	//       by the component where applyStyle() is called will be available.  You cannot pass in the
	//       name of a style from a parent component or a global stylesheet.
	if (_.isString(fnOrStyle)) {
		const newClassName = fnOrStyle + ' ' + _.get(defaultStyle, 'className', '');
		return {className: newClassName, styles: defaultStyle.style};
	}

	// If it's an object, it will be spread onto the object
	if (_.isObject(fnOrStyle)) {
		// TODO: Should we restrict the fields here to `className` and `style`?
		return {...defaultStyle, ...fnOrStyle};
	}

	return defaultStyle;
}

// Merge the current template values with the those for the specified `templateName`.
// This is done in preparation of rendering a template.
export function overrideTemplates(templateName, templates) {
	if (!templates) {
		templates = {};
	}
	return {...templates, ..._.get(templates, [templateName, 'templates'], {})};
}

// Apply the template overrides, then call the template render function.
export function renderTemplate(templateName, templates, context) {
	const nestedTemplates = overrideTemplates(templateName, templates);

	const templateFuncOrObj = templates[templateName];
	if (_.isFunction(templateFuncOrObj)) {
		// Use the function directly
		return templateFuncOrObj({...context, templates: nestedTemplates});
	}

	// If the template is specified as an object, then it should have a render function
	let render = templateFuncOrObj.render;
	if (!_.isFunction(render)) {
		console.error(
			`ERROR: render function in template ${templateName} must be a function.`
		);
		render = () => undefined;
	}

	const properties = {
		...context.properties,
		..._.get(templates, [templateName, 'properties'], {})
	};

	return render({...context, templates: nestedTemplates, properties});
}

export const arrayToMap = (array = []) =>
	array.reduce((obj, key) => ({...obj, [key]: ''}), {});
