import '@servicenow/now-button';
import {
	BUTTON_CLICKED,
	CLOSE_PANEL,
	LIST_REFRESH_REQUESTED_INTERNAL,
	PANEL_TYPE_FILTER
} from '../../constants';
import {createCustomElement} from '@servicenow/ui-core';
import {
	dirtyBehavior,
	dirtyModalInterceptor
} from '../../behaviors/dirtyBehavior/dirtyBehavior';
import {t} from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from '../filterPanelButton/filterPanelButton.scss';

const REFRESH_LIST = 'Refresh List';

const view = ({properties}) => {
	const {
		size,
		variant,
		liveListCount,
		liveLists,
		hideLiveList,
		listTitle
	} = properties;
	const refreshAriaLabel = t(REFRESH_LIST + ' ' + listTitle);
	const liveListsAriaLabel = liveLists
		? t(`Refresh List ${listTitle} (${liveListCount} updates available)`)
		: refreshAriaLabel;
	const ariaConfig = {
		'aria-label': liveListsAriaLabel
	};

	return (
		<div className={`sn-filter-panel-button -${size}`}>
			<now-button
				tooltip-content={REFRESH_LIST}
				config-aria={ariaConfig}
				icon-name="sync-outline"
				size={size}
				variant={variant}
			/>
			{!hideLiveList && liveListCount > 0 ? (
				<now-badge
					size="sm"
					status="info"
					value={liveListCount}
					variant="secondary"
					round
				/>
			) : null}
		</div>
	);
};

createCustomElement('sn-record-list-header-toolbar-button-refresh', {
	properties: {
		size: {default: 'md'},
		variant: {default: 'secondary'},
		liveLists: {default: false},
		liveListCount: {default: 0},
		hideLiveList: {default: true},
		panelType: {default: ''},
		listTitle: {default: ''}
	},
	renderer: {
		type: snabbdom,
		view
	},
	actionHandlers: {
		[BUTTON_CLICKED]: {
			effect: ({dispatch, properties: {panelType}}) => {
				if (panelType !== PANEL_TYPE_FILTER) dispatch(CLOSE_PANEL);
				dispatch(LIST_REFRESH_REQUESTED_INTERNAL, {
					timestamp: Date.now(),
					fromButton: true
				});
			},
			stopPropagation: true,
			interceptors: [dirtyModalInterceptor]
		}
	},
	behaviors: [dirtyBehavior],
	styles: `${style}`
});
