import '@servicenow/now-button';
import {DECLARATIVE_ACTIONS} from '../constants';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {Logger} from '../../../utils/logger';
import {getButtonAttributes} from '../helpers';
import isEmpty from 'lodash/isEmpty';

const LOG = Logger().createLog('DeclarativeActionsButton');

const DeclarativeActionsButton = ({action, count, model, variant}) => {
	if (isEmpty(action)) return null;

	const {name, toolTip} = action;
	const button = getButtonAttributes(action, count);

	LOG.log('Action/Model: ', action, model);

	return (
		<Fragment>
			<now-button
				variant={variant}
				size={DECLARATIVE_ACTIONS.BUTTON_SIZE}
				label={button.label}
				component-name={DECLARATIVE_ACTIONS.BUTTON + `-${name}`}
				append-to-payload={{action, model}}
				{...button.disabled}
				tooltip-content={toolTip}
			/>
		</Fragment>
	);
};

export default DeclarativeActionsButton;
