import {
	ALLOW_CURRENT_TABBABLE,
	UPDATE_TABBABLES
} from '../../behaviors/constants';
import {
	COL_RESIZE_INITIAL,
	CONTENT_ITEMS_SELECTED,
	DECLARATIVE_ACTION_UPDATE_RENDER,
	FORM_DATA_PROVIDER_SUCCESS,
	FORM_UPDATE_HEADER,
	GFORM_INTERNAL_BATCH,
	GRAPHQL_REQUEST_STARTED,
	LIST_CONNECTED_FETCH_REQUESTED,
	LIST_CONNECTED_FETCH_SUCCEEDED,
	LIST_DATA_FETCH_COMPLETE,
	LIST_FORM_EDIT_PREVIEW_RECORD_CLICKED,
	LIST_FORM_EDIT_WRAPPED_CLICKED,
	MULTI_FORM,
	NOW_BUTTON_ICONIC_CLICKED,
	NOW_DROPDOWN_ITEM_CLICKED,
	NOW_DROPDOWN_LIST_ACTIVE_ITEM_SET,
	NOW_DROPDOWN_LIST_ITEM_CLICKED,
	NOW_INPUT_INVALID,
	NOW_PANEL_OPENED_SET,
	NOW_POPOVER_CONTENT_HIDDEN,
	NOW_POPOVER_CONTENT_VISIBLE,
	NOW_RECORD_DATE_PICKER_STAGED_CHANGED,
	NOW_RECORD_MINI_CALENDAR_OK,
	NOW_RESIZE_ELEMENT_RESIZED,
	NOW_TOOLTIP_OPENED_SET,
	OPEN_PANEL,
	PREVIEW_RECORD,
	QUICK_EDIT_CLICKED,
	QUICK_FORM,
	RECORD_WATCHER_FAILED,
	RECORD_WATCHER_STARTED,
	RECORD_WATCHER_SUBSCRIBED
} from '../../constants';
import {
	NOW_GRID_DOM_READY,
	NOW_GRID_TREE_READY,
	NOW_GRID_TREE_RENDERED,
	NOW_SORTABLE_UPDATE_BEHAVIOR
} from '../nowGrid/constants';
import {OPEN_DIRTY_MODAL} from '../../behaviors/dirtyBehavior/dirtyModalConstants';
import {actionTypes} from '@servicenow/ui-core';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

const {COMPONENT_PROPERTY_CHANGED, COMPONENT_ACTION_DISPATCHED} = actionTypes;
const DA_FIELD_ACTION_TAG = 'SN-DECLARATIVE-FIELD-ACTION';

const IGNORED_EVENTS = {
	['undefined']: 1,
	[RECORD_WATCHER_SUBSCRIBED]: 1,
	[RECORD_WATCHER_FAILED]: 1,
	[RECORD_WATCHER_STARTED]: 1,
	[FORM_DATA_PROVIDER_SUCCESS]: 1,
	[FORM_UPDATE_HEADER]: 1,
	[CONTENT_ITEMS_SELECTED]: 1,
	[QUICK_FORM.CLOSE]: 1,
	[QUICK_EDIT_CLICKED]: 1,
	[LIST_FORM_EDIT_WRAPPED_CLICKED]: 1,
	[COMPONENT_PROPERTY_CHANGED]: 1,
	[OPEN_DIRTY_MODAL]: 1,
	[OPEN_PANEL]: 1,
	[MULTI_FORM.DIRTY_CHANGED]: 1,
	[MULTI_FORM.CLOSE]: 1,
	[NOW_SORTABLE_UPDATE_BEHAVIOR]: 1,
	[COL_RESIZE_INITIAL]: 1,
	[NOW_RESIZE_ELEMENT_RESIZED]: 1,
	[NOW_GRID_DOM_READY]: 1,
	[NOW_GRID_TREE_READY]: 1,
	[NOW_GRID_TREE_RENDERED]: 1,
	[NOW_PANEL_OPENED_SET]: 1,
	[UPDATE_TABBABLES]: 1,
	[ALLOW_CURRENT_TABBABLE]: 1,
	[NOW_TOOLTIP_OPENED_SET]: 1,
	[NOW_POPOVER_CONTENT_VISIBLE]: 1,
	[NOW_POPOVER_CONTENT_HIDDEN]: 1,
	[NOW_BUTTON_ICONIC_CLICKED]: 1,
	[NOW_DROPDOWN_LIST_ACTIVE_ITEM_SET]: 1,
	[NOW_DROPDOWN_LIST_ITEM_CLICKED]: 1,
	[NOW_DROPDOWN_ITEM_CLICKED]: 1,
	[NOW_INPUT_INVALID]: 1,
	[NOW_RECORD_DATE_PICKER_STAGED_CHANGED]: 1,
	[NOW_RECORD_MINI_CALENDAR_OK]: 1,
	[LIST_DATA_FETCH_COMPLETE]: 1,
	[LIST_CONNECTED_FETCH_SUCCEEDED]: 1,
	[LIST_CONNECTED_FETCH_REQUESTED]: 1,
	[GRAPHQL_REQUEST_STARTED]: 1,
	[GFORM_INTERNAL_BATCH]: 1,
	[DECLARATIVE_ACTION_UPDATE_RENDER]: 1
};

const isEditFormEvent = action => {
	const {meta, type} = action;

	if (isEmpty(type) || IGNORED_EVENTS[type]) return false;

	const event = get(meta, 'event', {});
	const path = event.composedPath ? event.composedPath() : event.path;
	const fromDA = path.some(item => {
		return item.tagName === DA_FIELD_ACTION_TAG;
	});

	return !!fromDA;
};

export const actionHandlers = {
	[PREVIEW_RECORD]: {
		effect: coeffects => {
			const {
				action,
				dispatch,
				properties: {workspaceConfigId}
			} = coeffects;

			if (!isEmpty(workspaceConfigId)) return;

			action.stopPropagation();
			dispatch(
				LIST_FORM_EDIT_PREVIEW_RECORD_CLICKED,
				get(action, 'payload', {})
			);
		}
	},
	[COMPONENT_ACTION_DISPATCHED]: {
		effect: coeffects => {
			const {
				action,
				action: {payload},
				dispatch,
				properties: {workspaceConfigId}
			} = coeffects;

			if (!isEmpty(workspaceConfigId)) return;

			if (isEditFormEvent(action)) {
				const wrappedAction = get(payload, 'action.actionDispatch');

				if (isEmpty(wrappedAction)) return;

				dispatch(LIST_FORM_EDIT_WRAPPED_CLICKED, {
					wrappedAction,
					wrappedPayload: get(payload, 'action.actionPayload', payload)
				});
			}
		}
	}
};
