import {actionHandlers} from './actions';
import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

const view = state => {
	const {
		properties: {quickEditSysId, table}
	} = state;

	return (
		<sn-record-common-quick-form
			table={table}
			sys-id={quickEditSysId}
			view={'workspace'}
			key={`quick_form_${quickEditSysId}`}
		/>
	);
};

createCustomElement('sn-record-list-quick-edit', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		quickEditSysId: {default: ''},
		table: {default: ''},
		workspaceConfigId: {default: ''}
	},
	actionHandlers
});
