import {CLOSE_MODAL, OPEN_MODAL} from '../constants';
import {actionTypes} from '@seismic/core';
const {COMPONENT_PROPERTY_CHANGED} = actionTypes;
import {MODAL_TYPES} from '../constants';
import {getAndClearRetainedFocus} from '../utils/focusUtility';
import get from 'lodash/get';
export const modalPropsChangedEffect = ({
	action,
	state: {modalProps},
	dispatch
}) => {
	const {
		payload: {value}
	} = action;
	if (modalProps && modalProps.type === MODAL_TYPES.CASCADE_DELETE) {
		const cascadeContent = get(value, 'cascadeContent', '');
		const recordsDeleted = get(value, 'recordsDeleted', false);
		if (cascadeContent) {
			dispatch(OPEN_MODAL, {
				modalProps: {
					...modalProps,
					content: cascadeContent
				}
			});
		} else if (recordsDeleted) dispatch(CLOSE_MODAL);
	}
};

const modalBehavior = {
	name: 'modalBehavior',
	properties: {},
	initialState: {
		modalProps: {
			type: ''
		}
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: {
			effect: modalPropsChangedEffect
		},
		[OPEN_MODAL]: {
			effect: ({action, updateState}) => {
				updateState({
					modalProps: {
						...action.payload.modalProps
					}
				});
			},
			stopPropagation: true
		},
		[CLOSE_MODAL]: {
			effect: ({updateState}) => {
				updateState({
					modalProps: {
						type: ''
					}
				});
			},
			interceptors: [
				{
					after: () => {
						getAndClearRetainedFocus('listActionsButton');
					}
				}
			],
			stopPropagation: true
		}
	}
};

export default modalBehavior;
