import {actionTypes} from '@servicenow/ui-core';
import {querySelector} from '@tectonic/seismic-element-helpers';

import {
	ALLOW_CURRENT_TABBABLE,
	FOCUS_ENTER_CELL,
	FOCUS_EXIT_CELL,
	GRID_BASE_SELECTOR,
	GRID_CONTROL_BEHAVIOR_NAME,
	KEY,
	MOVE_FOCUS,
	NOW_GRID_TAG,
	NOW_GRID_TREE_RENDERED,
	UPDATE_TABBABLES
} from './constants';

import {
	allowCurrentFocusEffect,
	clickHandlerEffect,
	focusEnterCellEffect,
	focusExitCellEffect,
	getCellFromGrid,
	getState,
	moveFocusEffect,
	propChangeHandler,
	updateTabbablesEffect
} from './actions/gridControlActions';

import get from 'lodash/get';

const {COMPONENT_PROPERTY_CHANGED, COMPONENT_CONNECTED} = actionTypes;

export const gridControls = {
	name: GRID_CONTROL_BEHAVIOR_NAME,
	initialState: {
		currentFocus: {
			row: 0,
			cell: 0,
			node: undefined
		},
		grid: {rows: []},
		trapcell: undefined
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect({action, dispatch, state}) {
				const event = get(action, 'payload.event', {});
				const path = event.composedPath ? event.composedPath() : event.path;
				const fromGrid = path.some(item => {
					return item.tagName === NOW_GRID_TAG;
				});
				const {trapcell, grid, currentFocus} = getState(state);
				const cell = getCellFromGrid({grid, currentFocus});
				let preventDefault = true;

				switch (event.key) {
					case KEY.UP:
					case KEY.ARROW_UP:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.UP});
						break;
					case KEY.DOWN:
					case KEY.ARROW_DOWN:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.DOWN});
						break;
					case KEY.LEFT:
					case KEY.ARROW_LEFT:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.LEFT});
						break;
					case KEY.RIGHT:
					case KEY.ARROW_RIGHT:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.RIGHT});
						break;
					case KEY.HOME:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.HOME});
						break;
					case KEY.END:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.END});
						break;
					case KEY.PAGE_UP:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.PAGE_UP});
						break;
					case KEY.PAGE_DOWN:
						if (!trapcell) dispatch(MOVE_FOCUS, {key: KEY.PAGE_DOWN});
						break;
					case KEY.SPACEBAR:
					case KEY.SPACE:
					case KEY.ENTER:
						if (!trapcell && cell.node === currentFocus.node && fromGrid)
							dispatch(FOCUS_ENTER_CELL);
						else preventDefault = false;
						break;
					case KEY.ESC:
					case KEY.ESCAPE:
						if (trapcell) dispatch(FOCUS_EXIT_CELL);
						else preventDefault = false;
						break;
					default:
						preventDefault = false;
						break;
				}

				if (preventDefault) event.preventDefault();
				event.stopPropagation();
			}
		},
		{
			events: ['click'],
			effect: clickHandlerEffect
		}
	],
	actionHandlers: {
		[UPDATE_TABBABLES]: {
			effect: updateTabbablesEffect
		},
		[ALLOW_CURRENT_TABBABLE]: {
			effect: allowCurrentFocusEffect
		},
		[MOVE_FOCUS]: {
			effect: moveFocusEffect
		},
		[FOCUS_ENTER_CELL]: {
			effect: focusEnterCellEffect
		},
		[FOCUS_EXIT_CELL]: {
			effect: focusExitCellEffect
		},
		[COMPONENT_PROPERTY_CHANGED]: propChangeHandler,
		[COMPONENT_CONNECTED]: ({host, dispatch}) => {
			dispatch(UPDATE_TABBABLES, {
				host: querySelector(GRID_BASE_SELECTOR, host)
			});
		},
		[NOW_GRID_TREE_RENDERED]: ({
			dispatch,
			host,
			state: {isRendered},
			updateState
		}) => {
			const gridNode = querySelector(GRID_BASE_SELECTOR, host);
			const gridNodeTbl = gridNode.shadowRoot.querySelector('tbody');
			if (gridNodeTbl && gridNodeTbl.children.length && !isRendered) {
				dispatch(UPDATE_TABBABLES, {
					host: gridNode
				});
				updateState({isRendered: true});
			}
		}
	}
};
