import {Logger} from '../../../utils/logger';
import {snHttpFactory} from 'sn-http-request';

const LOG = Logger().createLog('PollProcessorService');

const snHttp = snHttpFactory({
	xsrfToken: typeof window !== 'undefined' ? window.g_ck : undefined,
	batch: false,
	batching: false
});

const emailExportCfg = {
	excel: {
		sysparm_export: 'unload_excel_xlsx',
		processor_name: '/sys_confirm_excel.do'
	},
	json: {
		sysparm_export: 'unload_json',
		processor_name: '/sys_confirm_json.do'
	},
	csv: {
		sysparm_export: 'unload_csv',
		processor_name: '/sys_confirm_csv.do'
	},
	'pdf-portrait': {
		sysparm_export: 'unload_pdf',
		processor_name: '/sys_confirm_pdf.do'
	},
	'pdf-detailed portrait': {
		sysparm_export: 'unload_pdf+',
		processor_name: '/sys_confirm_pdf+.do'
	},
	'pdf-landscape': {
		sysparm_export: 'unload_pdflandscape',
		processor_name: '/sys_confirm_pdflandscape.do'
	},
	'pdf-detailed landscape': {
		sysparm_export: 'unload_pdf+landscape',
		processor_name: '/sys_confirm_pdf+landscape.do'
	}
};

const getExportParam = (fileType, orientation) => {
	let cfg =
		emailExportCfg[fileType + (fileType === 'pdf' ? '-' + orientation : '')];
	return cfg.sysparm_export;
};

const getEmailProcessor = (fileType, orientation) => {
	let cfg =
		emailExportCfg[fileType + (fileType === 'pdf' ? '-' + orientation : '')];
	return cfg.processor_name;
};

const pollProcessorService = (() => {
	const pollProcessor = (jobId, action) => {
		return snHttp.request('/poll_processor.do', 'POST', {
			params: {
				job_id: jobId,
				sys_action: action,
				sysparm_processor: 'poll_processor'
			},
			method: 'POST'
		});
	};

	const pollInit = options => {
		const {
			table,
			view,
			columns,
			rows,
			query,
			fileType,
			orientation,
			workspaceConfigId
		} = options;

		const pollOptions = {
			sysparm_target: table,
			sysparm_export: getExportParam(fileType, orientation),
			sysparm_rows: rows,
			sysparm_fields: columns || '',
			sysparm_view: view,
			sysparm_query: query || '',
			sys_action: 'init',
			sysparm_workspace_export: 'workspace',
			workspace_id: workspaceConfigId
		};

		LOG.log('List Export::PollInit()');

		return snHttp.request('/poll_processor.do', 'POST', {
			params: {...pollOptions},
			method: 'POST'
		});
	};

	return {
		poll: pollProcessor,
		init: pollInit
	};
})();

const emailProcessorService = (() => {
	const emailExport = options => {
		const {
			table,
			rows,
			view,
			query,
			email,
			fileType,
			orientation,
			columns,
			workspaceConfigId
		} = options;
		const processorUrl = getEmailProcessor(fileType, orientation);

		const pollOptions = {
			sysparm_target: table,
			sysparm_export: getExportParam(fileType, orientation),
			sysparm_rows: rows,
			sysparm_view: view,
			sysparm_fields: columns,
			sysparm_query: query,
			email_address: email,
			sys_action: 'email',
			sysparm_workspace_export: 'workspace',
			workspace_id: workspaceConfigId
		};
		return snHttp.request(processorUrl, 'POST', {
			params: {...pollOptions},
			method: 'POST'
		});
	};
	return {emailExport};
})();

export {pollProcessorService, emailProcessorService};
