import {actionTypes, createCustomElement} from '@servicenow/ui-core';
const {COMPONENT_PROPERTY_CHANGED, COMPONENT_DOM_READY} = actionTypes;
import {dirtyBehavior} from '../../behaviors/dirtyBehavior/dirtyBehavior';
import {wrapperTransformer} from './helpers';
import buttonActionHandlers from './actions/buttonActions';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import splitButtonActionHandlers from './actions/splitButtonActions';
import styles from './wrapper.scss';
import view from './views/wrapperView';

createCustomElement('sn-record-list-declarative-actions-wrapper', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [dirtyBehavior],
	properties: {
		actions: {default: []},
		count: {default: 0},
		model: {default: {}},
		isWorkspace: {default: false}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({properties, updateState, dispatch}) => {
			const transformedActions = wrapperTransformer(properties, dispatch);
			updateState(transformedActions);
		},
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				dispatch,
				action: {
					payload: {name, value, previousValue}
				},
				updateState,
				properties
			} = context;
			if (value === previousValue) return;

			if (name === 'actions' || name === 'count') {
				const transformedActions = wrapperTransformer(properties, dispatch);
				updateState(transformedActions);
			}
		},
		...buttonActionHandlers,
		...splitButtonActionHandlers
	},
	styles
});
