import {
	AFTER,
	AT_DELIMITER,
	AT_OR_AFTER,
	AT_OR_BEFORE,
	BEFORE,
	BETWEEN,
	DATE_FILTER,
	DATE_TIME_FILTER,
	ISEMPTY,
	ISNOTEMPTY,
	NOTON,
	ON,
	REQUIRES_RANGE,
	SCRIPT_DATE_GENERATE,
	SINGLE_QUOTE,
	TIME_END,
	TIME_START,
	_END,
	_START
} from '../constants';
import isEmpty from 'lodash/isEmpty';

const getDateTimeString = props => {
	const {type, field, operator, inputValue1: one, inputValue2: two} = props;
	if (_validation(type, field, operator, one)) return;

	if (_isEmptyOperator(operator)) return `${field + operator}`;

	if (REQUIRES_RANGE.indexOf(operator) === -1)
		return getScript(one, _getWhen(type, operator));

	return _isOnOperator(operator)
		? `${one + AT_DELIMITER + getStartAndEnd(one, one)}`
		: getStartAndEnd(one, isEmpty(two) ? '' : two);
};

const getScript = (value, when) =>
	`${SCRIPT_DATE_GENERATE}('${value}', '${when}')`;

const getStartAndEnd = (valueOne, valueTwo) =>
	`${getScript(valueOne, _START) + AT_DELIMITER + getScript(valueTwo, _END)}`;

const parseDateTimeString = (operator, value, getValues) => {
	if (operator === BETWEEN) {
		const splitValues = value.split(AT_DELIMITER);
		return getValues([
			parseSingle(splitValues[0], SINGLE_QUOTE),
			parseSingle(splitValues[1], SINGLE_QUOTE)
		]);
	}
	return getValues([], parseSingle(value, SINGLE_QUOTE));
};

const parseSingle = (value, delimiter) => {
	const splitValue = value.split(delimiter);
	return splitValue[1];
};

const _getWhen = (type, operator) => {
	if (type === DATE_FILTER) {
		if (operator === BEFORE || operator === AT_OR_BEFORE) return _START;
		if (operator === AFTER || operator === AT_OR_AFTER) return _END;
	}
	if (type === DATE_TIME_FILTER) {
		if (operator === BEFORE || operator === AT_OR_BEFORE) return TIME_START;
		if (operator === AFTER || operator === AT_OR_AFTER) return TIME_END;
	}
};

const _isOnOperator = operator => operator === ON || operator === NOTON;

const _isEmptyOperator = operator =>
	operator === ISEMPTY || operator === ISNOTEMPTY;

const _validation = (type, field, operator, inputValue1) =>
	isEmpty(type) || isEmpty(field) || isEmpty(operator) || isEmpty(inputValue1);

export {getDateTimeString, getScript, parseDateTimeString};
