import '@servicenow/now-button';
import '@servicenow/now-popover';
import {NOW_GRID_DOM_READY} from '../constants';
import {REFIT_POPOVER} from '../../../constants';
import {querySelector} from '@tectonic/seismic-element-helpers';
import get from 'lodash/get';
import set from 'lodash/set';
import throttle from 'lodash/throttle';

export const PLUGIN_NAME = 'popover';
export const BEHAVIOR_PATH = ['behaviors', PLUGIN_NAME];
export const OPTIONS_PATH = ['options', PLUGIN_NAME];
export const GRID_CLOSE_POPOVER = 'GRID_CLOSE_POPOVER';
export const LIST_SCROLL_UPDATED = 'LIST_SCROLL_UPDATED';
const KEY_ESCAPE = 'Escape';

export const behaviorUpdatePath = `behaviors.${PLUGIN_NAME}`;

const DEFAULT_POSITIONS = [
	{target: 'bottom-start', content: 'top-start'},
	{target: 'bottom-end', content: 'top-end'},
	{target: 'top-start', content: 'bottom-start'},
	{target: 'top-end', content: 'bottom-end'},
	{target: 'top-end', content: 'top-start'},
	{target: 'bottom-end', content: 'bottom-start'},
	{target: 'top-start', content: 'top-end'},
	{target: 'bottom-start', content: 'bottom-end'}
];

const getPopoverOpen = state =>
	get(state, [...BEHAVIOR_PATH, 'popoverOpen'], false);

const getDismissOnScroll = state =>
	get(state, [...BEHAVIOR_PATH, 'dismissOnScroll'], false);

const updatePopoverOpen = (updateState, value) => {
	updateState({
		path: `behaviors.${PLUGIN_NAME}.popoverOpen`,
		value: value,
		operation: 'set'
	});
};

const updateDismissOnScroll = (updateState, value) => {
	updateState({
		path: `behaviors.${PLUGIN_NAME}.dismissOnScroll`,
		value,
		operation: 'set'
	});
};

export const selectors = {
	getPopoverOpen,
	getDismissOnScroll
};

const DEFAULT_SELECTOR = '>>> now-grid >>> .container';

const getGridElement = (host, selector = DEFAULT_SELECTOR) => {
	return querySelector(selector, host);
};
/**
 * Keeps track of current list instance with value as object containing ref to grid
 */
const gridRefWeakMap = new WeakMap();

export const OPEN_POPOVER = 'OPEN_POPOVER';

const closePopoverEffect = coeffects => {
	const {
		action: {
			payload: {event}
		},
		updateState,
		host
	} = coeffects;

	const gridRef = getGridElement(host);
	if (gridRefWeakMap.has(gridRef)) {
		const {popoverRef, targetRef, contentRef} = gridRefWeakMap.get(gridRef);

		if (!popoverRef) return;

		const popoverOpen = popoverRef.getProperty('opened');
		if (popoverOpen) {
			/**
			 * Hoisted elements are not wrapped in the expected tag. Shadow dom of component that is hoisted is
			 * appeneded to seismic hoist component and is identifiable by seismicHoistElement.sourceElement.
			 * Here we check to see if there is a click event that comes from the same trigger or the popover content
			 * and if it is, we do not close the popover
			 */
			if (
				event &&
				event.path.filter(item => {
					// if date picker is clicked do not close popover
					if (item.className === 'date-picker-panel') return true;

					if (!item.isEqualNode) return false;

					const isNodePopover =
						item.tagName === 'SEISMIC-HOIST' &&
						contentRef.isSameNode(item.sourceElement);
					const isNodeTrigger = targetRef.isSameNode(item);
					return isNodePopover || isNodeTrigger;
				}).length > 0
			) {
				return;
			}

			updatePopoverOpen(updateState, false);
			popoverRef.opened = false;
		}
	}
};

const reduceActions = dismissableActions =>
	dismissableActions.reduce((acc, current) => {
		acc[current] = {
			effect: closePopoverEffect
		};
		return acc;
	}, {});

const getPopoverRefFromHost = host => {
	const gridRef = getGridElement(host);
	if (gridRefWeakMap.has(gridRef)) {
		const {popoverRef} = gridRefWeakMap.get(gridRef);
		return popoverRef;
	}
	return null;
};

const attachPopover = ({dispatch, host}) => {
	/**
	 * Currently now-popover attaches itself to the closest parent scroll. This will change in Q.
	 * For now we will
	 */
	const gridScrollElementRef = getGridElement(host);
	if (!gridScrollElementRef || gridRefWeakMap.has(gridScrollElementRef)) return;

	gridScrollElementRef.addEventListener(
		'scroll',
		throttle(() => {
			const timestamp = Date.now();
			dispatch(LIST_SCROLL_UPDATED, {
				timestamp
			});
		}, 1000)
	);

	const popoverRef = document.createElement('NOW-POPOVER');
	popoverRef.opened = false;
	popoverRef.manageOpened = true;
	gridScrollElementRef.appendChild(popoverRef);

	gridRefWeakMap.set(gridScrollElementRef, {popoverRef});
};

export function instantiatePlugin(
	options = {},
	popoverAttachmentSelector = DEFAULT_SELECTOR
) {
	const {dismissableActions = []} = options;
	const dismissActionHandlers = reduceActions(dismissableActions);

	return {
		transform(state) {
			return {
				...state,
				options: {
					...state.options,
					popover: get(state, BEHAVIOR_PATH, {})
				}
			};
		},
		behavior: {
			name: PLUGIN_NAME,
			initialState: {
				popoverOpen: false,
				dismissOnScroll: false
			},
			actionHandlers: {
				[NOW_GRID_DOM_READY]: {
					effect: attachPopover
				},
				[OPEN_POPOVER]: {
					effect: coeffects => {
						const {
							action: {
								payload: {
									ref,
									componentName,
									componentProps,
									dismissOnScroll = false,
									hideTail = false,
									positions = DEFAULT_POSITIONS
								}
							},
							updateState,
							state,
							host
						} = coeffects;

						const gridRef = getGridElement(host);

						if (!gridRefWeakMap.has(gridRef)) attachPopover(coeffects);

						if (gridRefWeakMap.has(gridRef)) {
							const {popoverRef, targetRef, contentRef} = gridRefWeakMap.get(
								gridRef
							);

							if (!popoverRef) return;
							// Checking component state due to updateState timing issues in event handler
							const popoverOpen = popoverRef.getProperty('opened');

							// Do not open popover if same target ref dispatched open action
							if (
								popoverOpen &&
								targetRef &&
								targetRef.isSameNode(ref.current)
							) {
								return;
							}

							// Toggle popover prop to refresh component
							popoverRef.opened = false;
							popoverRef.positions = positions;

							// Create component that was dispatched and set corresponding props
							const newContent = document.createElement(componentName);
							newContent.slot = 'content';
							Object.keys(componentProps).forEach(key => {
								newContent[key] = componentProps[key];
							});

							if (contentRef) popoverRef.replaceChild(newContent, contentRef);
							else popoverRef.appendChild(newContent);

							popoverRef.hideTail = hideTail;
							popoverRef.positionTarget = ref.current;
							popoverRef.opened = true;

							const gridData = {
								popoverRef,
								targetRef: ref.current,
								contentRef: newContent
							};
							gridRefWeakMap.set(gridRef, gridData);

							updateDismissOnScroll(updateState, dismissOnScroll);
							updatePopoverOpen(updateState, true);
						}
					},
					stopPropagation: true
				},
				[LIST_SCROLL_UPDATED]: {
					effect: ({state, host}) => {
						const popoverRef = getPopoverRefFromHost(host);
						if (!popoverRef) return;
						const popoverOpen = popoverRef.getProperty('opened');
						const dismissOnScroll = selectors.getDismissOnScroll(state);
						if (popoverOpen && dismissOnScroll) popoverRef.opened = false;
					}
				},
				[REFIT_POPOVER]: {
					effect: ({host}) => {
						const popoverRef = getPopoverRefFromHost(host);
						if (popoverRef && popoverRef.getProperty('opened'))
							popoverRef.refit();
					}
				},
				...dismissActionHandlers
			},
			eventHandlers: [
				{
					events: ['click'],
					effect: closePopoverEffect,
					target: document
				},
				{
					events: ['keyup'],
					effect({
						action: {
							payload: {event}
						},
						updateState,
						host
					}) {
						if (event.key === KEY_ESCAPE) {
							const gridRef = getGridElement(host);
							if (gridRefWeakMap.has(gridRef)) {
								const {popoverRef} = gridRefWeakMap.get(gridRef);
								if (!popoverRef) return;
								const popoverOpen = popoverRef.getProperty('opened', false);
								if (popoverOpen) {
									updatePopoverOpen(updateState, false);
									popoverRef.opened = false;
								}
							}
						}
					},
					target: document
				}
			]
		}
	};
}
