import '@servicenow/now-modal';
import {
	CLOSE_MODAL,
	LIST_EDIT_COLUMN_REQUESTED,
	LIST_MENU_CRUD,
	LIST_TIMEAGO_RESET,
	LIST_UPDATE_COLUMN_WIDTH_USER_PREF,
	MODAL_ACTIONS
} from '../../constants';
import {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_DISCONNECTED
} from '@seismic/core/lib/constants';
import {createCustomElement} from '@seismic/core';
import {t} from 'sn-translate';
import snabbdom from '@seismic/snabbdom-renderer';
import styles from './styles.scss';

const editModalTitle = t('Edit columns');

let eventListenerRef;

const footerActionClickedEffect = coeffects => {
	const {dispatch} = coeffects;
	dispatch(CLOSE_MODAL);
};

const messageHandler = (dispatch, properties) => {
	return e => {
		const {messageType, modalAction, data} = e.data;
		const {
			listTitle,
			table,
			columns,
			query,
			originalConditions,
			selectedListId,
			isWorkspace
		} = properties;
		if (
			messageType === 'IFRAME_MODAL_MESSAGE_TYPE' &&
			[
				'IFRAME_MODAL_ACTION_CONFIRMED',
				'IFRAME_MODAL_ACTION_CANCELED'
			].includes(modalAction)
		) {
			window.removeEventListener('message', eventListenerRef);
			eventListenerRef = null;
			const opts = {
				timestamp: Date.now(),
				type: 'UPDATE',
				options: {columns: data}
			};
			if (isWorkspace) {
				dispatch(LIST_MENU_CRUD, opts);
			} else {
				dispatch(LIST_EDIT_COLUMN_REQUESTED, {
					timestamp: Date.now(),
					currentListData: {
						title: listTitle,
						table,
						columns,
						conditions: originalConditions,
						selectedListId: selectedListId ? selectedListId : undefined
					},
					update: {
						columns: data
					}
				});
			}
			dispatch(LIST_TIMEAGO_RESET, {timestamp: Date.now()});
			dispatch(CLOSE_MODAL);
			dispatch(LIST_UPDATE_COLUMN_WIDTH_USER_PREF, {columnWidths: []});
		}
	};
};

const view = (state, {dispatch}) => {
	const {url} = state.properties;

	let iFrameRef, iFramePathName;
	let initialized = false;
	const iFrameOnLoad = () => {
		if (!initialized) {
			initialized = true;
			iFramePathName = iFrameRef.contentWindow.location.pathname;
		} else if (iFrameRef.contentWindow.location.pathname !== iFramePathName) {
			dispatch(CLOSE_MODAL);
		}
	};

	return (
		<now-modal
			size="md"
			header-label={editModalTitle}
			manage-opened
			opened={true}>
			{url ? (
				<iframe
					src={url}
					sandbox="allow-same-origin allow-forms allow-scripts allow-popups-to-escape-sandbox allow-popups allow-modals"
					className={`sn-modal-iframe`}
					hook-insert={vnode => {
						iFrameRef = vnode.elm;
						iFramePathName = iFrameRef.contentWindow.location.pathname;
						iFrameRef.addEventListener('load', iFrameOnLoad);
					}}
				/>
			) : null}
		</now-modal>
	);
};

createCustomElement('sn-record-list-modal-edit', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		url: {},
		listTitle: {default: ''},
		table: {default: ''},
		columns: {default: ''},
		query: {default: ''},
		originalConditions: {default: ''},
		selectedListId: {default: ''},
		isWorkspace: {default: false}
	},
	actionHandlers: {
		[MODAL_ACTIONS.FOOTER_ACTION_CLICKED]: {
			effect: footerActionClickedEffect,
			stopPropagation: true
		},
		[MODAL_ACTIONS.OPENED_SET]: {
			effect: footerActionClickedEffect,
			stopPropagation: true
		},
		[COMPONENT_BOOTSTRAPPED]: {
			effect: coeffects => {
				const {dispatch, properties} = coeffects;
				eventListenerRef = messageHandler(dispatch, properties);
				window.addEventListener('message', eventListenerRef);
			},
			stopPropagation: true
		},
		[COMPONENT_DISCONNECTED]: {
			effect: coeffects => {
				if (eventListenerRef)
					window.removeEventListener('message', eventListenerRef);
			}
		}
	},
	styles
});
