import {UPDATE_FOCUS_TRAP} from '../../../../utils/focusUtility';
import {createCustomElement} from '@servicenow/ui-core';
import {default as view} from './inputView';
import actionHandlers from './inputActions';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './input.scss';

createCustomElement('sn-record-list-column-filter-choice-input', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		name: {default: ''},
		inputValue: {default: '', reflect: true}
	},
	onPropertiesSet(host, dispatch) {
		dispatch(UPDATE_FOCUS_TRAP);
	},
	actionHandlers,
	styles
});
