import './subcomponents/input';
import '@servicenow/now-button';
import {
	APPLY,
	CLEAR,
	COL_FILTER_SELECTED,
	FILTER_APPLY_BUTTON,
	FILTER_CLEAR_BUTTON
} from '../constants';
import {DATE_FILTER, DATE_TIME_FILTER} from '../../../constants';
import {Fragment} from '@seismic/snabbdom-renderer';
// eslint-disable-next-line no-unused-vars
import {SNChoice} from '@devsnc/sn-form-controls';
import {renderGroupBy} from '../helpers';
import {t} from 'sn-translate';

const ColumnValueFilter = (state, {dispatch}) => {
	const {
		COL_TYPE_FILTERS,
		canClear,
		hasValidInput,
		isRange,
		requiresInput,
		filterIndex,
		properties,
		properties: {
			dateTimeFormat,
			displayValue,
			inputValue1,
			inputValue2,
			operator,
			type,
			isGroupable,
			isFilterable,
			hideColumnFiltering
		}
	} = state;
	const applyBtnDisabledAttr = hasValidInput ? {} : {disabled: true};

	const renderInputs = () => {
		return (
			<Fragment>
				<sn-record-list-column-filter-value-input
					date-time-format={dateTimeFormat}
					name="inputValue1"
					type={type}
					inputValue={inputValue1}
					hasValidInput={hasValidInput}
					isRange={isRange}
					filterIndex={filterIndex}
					requiresInput={requiresInput}
				/>
				{isRange ? (
					<sn-record-list-column-filter-value-input
						date-time-format={dateTimeFormat}
						name="inputValue2"
						type={type}
						inputValue={inputValue2}
						hasValidInput={hasValidInput}
						isRange={isRange}
						filterIndex={filterIndex}
						requiresInput={requiresInput}
					/>
				) : null}
			</Fragment>
		);
	};

	const getAriaConfig = elementName => {
		return {
			'aria-label': t(
				'{0} filter {1} on column {2}',
				elementName,
				operator,
				displayValue
			)
		};
	};

	const renderFilter = () => {
		if (!isFilterable || hideColumnFiltering) return null;

		return (
			<Fragment>
				<div
					className={`filter-container-filter-title header filter-text${
						isGroupable ? ' mgn-md' : ''
					}`}>
					{t('Filter')}
				</div>
				<sn-record-choice
					className="filter-select snchoice"
					style={{width: '100%'}}
					value={filterIndex}
					display-value={
						COL_TYPE_FILTERS[filterIndex].label[
							type === DATE_TIME_FILTER ? DATE_FILTER : type
						]
					}
					choices={COL_TYPE_FILTERS.map(filter => ({
						value: filter.operator,
						displayValue:
							filter.label[type === DATE_TIME_FILTER ? DATE_FILTER : type]
					}))}
					onValueChange={e => dispatch(COL_FILTER_SELECTED, e.value)}
				/>

				{requiresInput ? renderInputs() : null}

				<div
					className={`filter-container-buttons filter-buttons-container ${
						requiresInput ? 'mgn-lg' : 'mgn-md'
					}`}>
					{canClear ? (
						<div className="remove-filter">
							<now-button-bare
								className="clear-button"
								config-aria={getAriaConfig(CLEAR)}
								variant="secondary"
								size="sm"
								label={CLEAR}
								component-name={FILTER_CLEAR_BUTTON}
							/>
						</div>
					) : null}
					<now-button
						className="apply-button"
						config-aria={getAriaConfig(APPLY)}
						variant="primary"
						size="sm"
						label={APPLY}
						append-to-payload={{requiresInput, isRange, filterIndex}}
						component-name={FILTER_APPLY_BUTTON}
						key={`applyButton-${applyBtnDisabledAttr.disabled}`}
						{...applyBtnDisabledAttr}
					/>
				</div>
			</Fragment>
		);
	};

	return (
		<div className="filter-container">
			{renderGroupBy(properties)}
			{renderFilter(properties)}
		</div>
	);
};

export default ColumnValueFilter;
