import {DECLARATIVE_ACTIONS, OPEN_PARENTHESIS} from './constants';
import {LIST_DA_ACTIONS_CONTRACT_GENERATED} from '../../constants';
import {Logger} from '../../utils/logger';
import {contractGenerator, transformer} from '@devsnc/sn-declarative-actions';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import map from 'lodash/map';

const LOG = Logger().createLog('Helpers');

export const dispatchToMediator = ({action}, {model}, dispatch) => {
	LOG.log('Dispatching to DA Mediator... ', {
		action,
		model
	});
	dispatch(DECLARATIVE_ACTIONS.DISPATCH, {
		action,
		model
	});
};

export const getButtonAttributes = (
	{label, recordSelectionRequired},
	count
) => {
	const templateIndex = label.indexOf(OPEN_PARENTHESIS);
	const templateFound = templateIndex !== -1;
	let newLabel = label;
	if (count === 0 && templateFound) {
		newLabel = label.substring(
			0,
			templateIndex === 0 ? templateIndex : templateIndex - 1
		);
	}

	return {
		label: newLabel,
		disabled: recordSelectionRequired && count === 0 ? {disabled: true} : {}
	};
};

const processActions = (actions, model) => {
	const transformedActionsFlattened = [];
	const transformedActions = [];

	actions.forEach(action => {
		if (isEmpty(get(action, 'groupId'))) {
			const newAction = transformer([action], model)[0];
			transformedActionsFlattened.push(newAction);
			transformedActions.push(newAction);
		} else {
			const {group, groupId, order} = action;
			const groupedActions = transformer(action.groupedActions, model);
			transformedActionsFlattened.push(...groupedActions);
			transformedActions.push({
				group,
				groupId,
				groupedActions,
				order
			});
		}
	});
	LOG.log('Transformed Actions: ', transformedActionsFlattened);
	return {
		transformedActionsFlattened,
		transformedActions
	};
};

export const wrapperTransformer = (properties, dispatch) => {
	if (isEmpty(properties)) return {transformedActions: []};
	const {actions = [], isWorkspace, model} = properties;

	const {transformedActionsFlattened, transformedActions} = processActions(
		actions,
		model
	);

	if (!isWorkspace) {
		const daClientActionContract = contractGenerator(
			transformedActionsFlattened,
			model
		);
		LOG.log('Client Actions Contract: ', daClientActionContract);
		dispatch(LIST_DA_ACTIONS_CONTRACT_GENERATED, daClientActionContract);
	}

	return {
		transformedActions
	};
};
