import '@devsnc/sn-form-controls';
import '@servicenow/now-modal';
import {
	CLOSE_MODAL,
	LIST_MENU_CRUD,
	LIST_RENAME_REQUESTED,
	MODAL_ACTIONS,
	REFRESH_LIST_MENU
} from '../../constants';
import {
	COMPONENT_BOOTSTRAPPED,
	PROPERTIES_SET
} from '@seismic/core/lib/constants';
import {createCustomElement} from '@seismic/core';
import {t} from 'sn-translate';
import get from 'lodash/get';
import snabbdom from '@seismic/snabbdom-renderer';

const renameModalTitle = t('Rename');
const cancelTitle = t('Cancel');
const confirmTitle = t('Save');
const label = t('Title');

const view = (state, {updateState}) => {
	const {currentFieldValue} = state;

	const footerActions = [
		{
			label: confirmTitle,
			variant: 'primary',
			...(currentFieldValue.length === 0 ? {disabled: 'true'} : {})
		},
		{label: cancelTitle, variant: 'secondary'}
	];

	return (
		<now-modal
			size="sm"
			header-label={renameModalTitle}
			footer-actions={footerActions}
			manage-opened
			opened={true}>
			<sn-record-input
				value={currentFieldValue}
				name={'title'}
				mandatory={true}
				label={label}
				maxlength={40}
				onValueChange={e => {
					if (e && e.value.length <= 40) {
						updateState({
							path: 'currentFieldValue',
							value: e.value,
							operation: 'set'
						});
					}
					e.event.stopPropagation();
				}}
			/>
		</now-modal>
	);
};

const footerActionClickedEffect = coeffects => {
	const {dispatch, properties, state} = coeffects;
	const {currentFieldValue: title} = state;
	const {
		listTitle,
		table,
		columns,
		query,
		originalConditions,
		selectedListId,
		isWorkspace
	} = properties;
	const footerActionLabel = get(coeffects, 'action.payload.footerAction.label');
	if (footerActionLabel === confirmTitle) {
		const opts = {
			timestamp: Date.now(),
			type: 'UPDATE',
			options: {title}
		};
		if (isWorkspace) {
			dispatch(LIST_MENU_CRUD, opts);
		} else {
			dispatch(LIST_RENAME_REQUESTED, {
				timestamp: Date.now(),
				currentListData: {
					title: listTitle,
					table,
					columns,
					conditions: originalConditions,
					selectedListId: selectedListId ? selectedListId : undefined
				},
				update: {
					title
				}
			});
		}
	}
	dispatch(PROPERTIES_SET, {listTitle: title});
	dispatch(REFRESH_LIST_MENU, {refreshTimestamp: Date.now()});
	dispatch(CLOSE_MODAL);
};

const bootstrappedEffect = coeffects => {
	const {title} = coeffects.state.properties;
	coeffects.updateState({
		path: 'currentFieldValue',
		value: title,
		operation: 'set'
	});
};

createCustomElement('sn-record-list-modal-rename', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		currentFieldValue: ''
	},
	properties: {
		title: {default: ''},
		listTitle: {default: ''},
		table: {default: ''},
		columns: {default: ''},
		query: {default: ''},
		originalConditions: {default: ''},
		selectedListId: {default: ''},
		isWorkspace: {default: false}
	},
	actionHandlers: {
		[MODAL_ACTIONS.FOOTER_ACTION_CLICKED]: {
			effect: footerActionClickedEffect,
			stopPropagation: true
		},
		[MODAL_ACTIONS.OPENED_SET]: {
			effect: footerActionClickedEffect,
			stopPropagation: true
		},
		[COMPONENT_BOOTSTRAPPED]: {
			effect: bootstrappedEffect,
			stopPropagation: true
		}
	}
});
