import '../cellFiltering/cellFilteringContainer';
import {
	CELL_FILTERING,
	CELL_FILTERING_WIDTH,
	COLUMN_FILTERING,
	COLUMN_FILTERING_WIDTH,
	GRID_CLOSE_POPOVER,
	GRID_POPOVER_TRIGGER_OFFSET,
	GRID_POPOVER_TRIGGER_WIDTH,
	MODEL_PATH
} from '../../constants';
import {Logger} from '../../utils/logger';
import {eventPath} from '../../utils/clickHandlerHelpers';
import {getAndClearRetainedFocus} from '../../utils/focusUtility';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

const LOG = Logger().createLog('POPOVER');

export const popoverDocClickHandler = ({dispatch}) => evt => {
	try {
		const getEventPath = eventPath(window);
		const path = getEventPath(evt);

		if (
			path.filter(item =>
				item.classList
					? item.classList.contains('sn-grid-popover-container') ||
					  item.classList.contains('sn-grid-popover-trigger') ||
					  item.classList.contains('sn-popover')
					: false
			).length > 0
		)
			return;

		dispatch(GRID_CLOSE_POPOVER);
	} catch (ex) {
		LOG.log(`ERROR: failed to parse event path: ${ex}`);
	}
};

const popoverLeftOffsetByType = type => {
	switch (type) {
		case CELL_FILTERING:
			return CELL_FILTERING_WIDTH - GRID_POPOVER_TRIGGER_WIDTH;
		case COLUMN_FILTERING:
			return COLUMN_FILTERING_WIDTH - GRID_POPOVER_TRIGGER_WIDTH;
		default:
			return 0;
	}
};

const renderCellFilteringPopoverContents = (popover, listInstanceId) => (
	<sn-record-list-cell-filter
		popover={popover}
		list-instance-id={listInstanceId}
	/>
);

const renderColumnFilteringPopoverContents = (
	popover,
	gridModel,
	parsedQueryModel,
	hideColumnGrouping,
	hideColumnFiltering,
	listInstanceId,
	table,
	nowTableReturnFocus,
	isGlideQuery,
	nonGlideFilterProps
) => (
	<sn-record-list-column-filter
		column={popover.context}
		table={table}
		is-grouped={
			gridModel.tableMetadata.groupedColumn === popover.context.columnName
		}
		parsed-query-model={parsedQueryModel}
		filter-id={popover.context.columnName}
		list-instance-id={listInstanceId}
		is-filterable={popover.context.columnData.isFilterable}
		is-groupable={
			popover.context.columnData.isGroupable &&
			get(gridModel, MODEL_PATH.LAYOUT_QUERY.COUNT) > 0
		}
		hideColumnGrouping={hideColumnGrouping}
		hideColumnFiltering={hideColumnFiltering}
		nowTableReturnFocus={nowTableReturnFocus}
		isGlideQuery={isGlideQuery}
		nonGlideFilterProps={nonGlideFilterProps}
	/>
);

export const renderPopover = ({
	properties: {popover, isGlideQuery, nonGlideFilterProps},
	gridModel,
	parsedQueryModel,
	dispatch,
	hideColumnGrouping,
	hideColumnFiltering,
	listInstanceId,
	table,
	nowTableReturnFocus
}) => {
	if (isEmpty(popover)) {
		getAndClearRetainedFocus(listInstanceId);
		return null;
	}

	const role = popover.type === CELL_FILTERING ? '' : 'dialog';

	const styles = {
		position: 'absolute',
		left: `${popover.location.left - popoverLeftOffsetByType(popover.type)}px`,
		top: `${popover.location.top + GRID_POPOVER_TRIGGER_OFFSET}px`,
		backgroundColor: 'white',
		zIndex: `${popover.type === CELL_FILTERING ? 1 : 1060}`
	};
	return (
		<div
			role={role}
			on-keyup={evt => {
				if (evt.key === 'Esc' || evt.key === 'Escape') {
					dispatch(GRID_CLOSE_POPOVER);
					getAndClearRetainedFocus(listInstanceId);
				}
			}}
			style={styles}
			className="sn-grid-popover-container">
			{popover.type === CELL_FILTERING
				? renderCellFilteringPopoverContents(popover, listInstanceId)
				: null}
			{popover.type === COLUMN_FILTERING
				? renderColumnFilteringPopoverContents(
						popover,
						gridModel,
						parsedQueryModel,
						hideColumnGrouping,
						hideColumnFiltering,
						listInstanceId,
						table,
						nowTableReturnFocus,
						isGlideQuery,
						nonGlideFilterProps
				  )
				: null}
		</div>
	);
};
