import '@servicenow/now-badge';
import '@servicenow/now-button';

import {
	BUTTON_CLICKED,
	CLOSE_PANEL,
	OPEN_PANEL,
	PANEL_TYPE_FILTER,
	RESIZE_FLOATING_HEADER
} from '../../constants';
import {addRetainedElement} from '../../utils/focusUtility';
import {createCustomElement} from '@servicenow/ui-core';
import {
	dirtyBehavior,
	dirtyModalInterceptor
} from '../../behaviors/dirtyBehavior/dirtyBehavior';
import {t} from 'sn-translate';
import cuid from 'cuid';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from './filterPanelButton.scss';

const refreshButtonClickEffect = coeffect => {
	const {
		properties: {panelOpened, panelConfig},
		dispatch
	} = coeffect;

	const isFilterPanelOpen =
		panelOpened && panelConfig.panelType === PANEL_TYPE_FILTER;

	if (isFilterPanelOpen) {
		dispatch(CLOSE_PANEL);
		return;
	}

	dispatch(OPEN_PANEL, {
		panelType: PANEL_TYPE_FILTER
	});

	dispatch(RESIZE_FLOATING_HEADER, {
		UUID: cuid()
	});
};

const view = state => {
	const {
		count,
		panelOpened,
		panelConfig,
		size,
		variant,
		listTitle
	} = state.properties;

	const isFilterPanelOpen =
		panelOpened && panelConfig.panelType === PANEL_TYPE_FILTER;

	const filterDescription = isFilterPanelOpen
		? t(`Hide filter panel for ${listTitle} (${count} conditions)`)
		: t(`Show filter panel for ${listTitle} (${count} conditions)`);

	const ariaConfig = {
		'aria-expanded': 'true',
		'aria-controls': 'panel',
		'aria-label': filterDescription,
		'aria-haspopup': 'true'
	};

	return (
		<div className={`sn-filter-panel-button -${size}`}>
			<now-button
				icon-name="filter-outline"
				size={size}
				variant={variant}
				tooltip-content={filterDescription}
				config-aria={ariaConfig}
				ref={el => {
					if (el) {
						const button = el.shadowRoot.querySelector('button');
						addRetainedElement('filterPanelButton', button);
					}
				}}
			/>
			{count > 0 ? (
				<now-badge
					size="sm"
					status="info"
					value={count}
					variant="secondary"
					round
				/>
			) : null}
		</div>
	);
};

createCustomElement('sn-record-list-header-toolbar-button-filter-panel', {
	properties: {
		count: {default: 0},
		size: {default: 'md'},
		variant: {default: 'secondary'},
		panelOpened: {default: false},
		panelConfig: {default: {}},
		listTitle: {default: ''}
	},
	renderer: {
		type: snabbdom,
		view
	},
	actionHandlers: {
		[BUTTON_CLICKED]: {
			effect: refreshButtonClickEffect,
			interceptors: [dirtyModalInterceptor]
		}
	},
	behaviors: [dirtyBehavior],
	styles: `${style}`
});
