import '../declarativeActions/wrapper';
import '../grid/grid';
import '../grid/treeGrid';
import '../listErrorState/listErrorState';
import '../listHeaderTitle/listHeaderTitle';
import '../listHeaderToolbar/listHeaderToolbar';
import '../modals/listModal';
import '../slidingMenu/slidingMenuButton';
import '@devsnc/sn-declarative-actions';
import '@servicenow/now-pagination';
import '@servicenow/now-popover';
import {
	FINAL_COUNT,
	IS_OMIT_COUNT,
	ITEM_SELECTED,
	LIST_LOADER_SIZE_LG,
	LIST_VIEW_ALL_CLICKED,
	MODEL_PATH
} from '../../constants';

import '../listLoader/listLoader';
import '../nowGrid/overlay/components/loadingOverlay';
import '@servicenow/now-record-list-panel';
import {
	areRowSelectorsNeeded,
	getPaginationModel,
	isViewAllNeeded
} from './listUtils';
import {renderSpinnerContainer} from '../../utils/listLoaderHelper';
import {stripTextQuery} from './listModifierUtils';
import {t} from 'sn-translate';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

import '../columnFiltering/container';
import '../columnFiltering/value/valueFilter';
import '@servicenow/now-grid';
import {memoizeDeep} from 'seismic-memo-utils';
import {selectors} from '../nowGrid/recordDataTransform/recordDataTransform';

const renderErrorState = errorObject => {
	const message = get(errorObject, 'message', '');
	const extraInfo = get(errorObject, 'extraInfo', '');
	return (
		<sn-record-list-state-error message={message} extra-info={extraInfo} />
	);
};

const memoizedDAProps = memoizeDeep(daProps => daProps);
const memoziedHeaderTitleProps = memoizeDeep(
	headerTitleProps => headerTitleProps
);

const view = (state, {dispatch}) => {
	let {
		properties: {
			actionConfigId,
			allRecordsSelected,
			customCellRenderer,
			columns,
			columnWidths,
			columnWidthsResetRequested,
			dataUpdatedTime,
			declarativeActions,
			directLink,
			exceptedRecords,
			selectionCount,
			selectionQuery,
			hideCellFilter,
			hideColumnSorting,
			hideColumnGrouping,
			hideColumnFiltering,
			hideColumnResizing,
			hideRowSelector,
			hideLinks,
			hideHighlightedValues,
			hideTitleRowCount,
			hideTitle,
			hideLastRefreshedText,
			hideCheckboxHover,
			hideFilterPanel,
			hideHeader,
			hideMenuButton,
			hideDeclarativeActions,
			hideLiveList,
			hidePagination,
			hidePanel,
			hideQuickEdit,
			hideSelectAll,
			hideShiftRecordSelection,
			highlightContent,
			error,
			columnsResized,
			instanceId,
			isParentReadOnly,
			isParentNewRecord,
			isRefList,
			isWorkspace,
			limit,
			listMenuOpen,
			listModel,
			listTitle,
			listType,
			liveLists,
			liveListUpdates,
			liveListCount,
			loading,
			maxCharLimit,
			maxColumns,
			menuSelection,
			orderBy,
			originalConditions,
			panelOpened,
			page,
			panelConfig,
			parentRecordSysId,
			parentTable,
			parsedQueryModel,
			quickEditSysId,
			relatedListName,
			scrollHandlerThrottle,
			selectedListId,
			selectedRecords,
			table,
			timeAccessed,
			view,
			wordWrap,
			workspaceConfigId,
			hideRange,
			hidePages,
			hideRowCount,
			hideFirstPage,
			hidePreviousPage,
			hideNextPage,
			hideLastPage,
			hideLimitSelector,
			hideUnnecessaryRowSelectors,
			hideRefreshButton,
			hideListSharing,
			hideEmptyStateImage,
			hideViewAll,
			headerSize,
			headingLevel,
			cascadeDelete
		},
		templates,
		modalProps,
		useNowGrid,
		pluginStyles = {}
	} = state;

	const isScriptableTable = get(
		listModel,
		'tableMetadata.isScriptableTable',
		false
	);
	if (isScriptableTable) error = get(listModel, 'errorMessage', null) || error;

	if (!isEmpty(error))
		return (
			<div className="sn-list-container sn-list-loader-container">
				{renderErrorState(error)}
			</div>
		);

	if ((isEmpty(listModel) && loading) || get(listModel, 'empty', false))
		return (
			<div className="sn-list-container sn-list-loader-container">
				{renderSpinnerContainer(LIST_LOADER_SIZE_LG)}
			</div>
		);

	if (isEmpty(listModel) && !loading) return null;

	const shouldHideRowSelectors = !areRowSelectorsNeeded(
		hideRowSelector,
		hideDeclarativeActions,
		hideUnnecessaryRowSelectors,
		declarativeActions
	);

	const ariaTitle = listTitle
		? `${listTitle} List - ${table} Table`
		: `${table} Table`;

	const isGrouped = get(listModel, 'tableMetadata.isGrouped', false);
	const rowCount = get(listModel, MODEL_PATH.LAYOUT_QUERY.COUNT, 0);
	const recordCount = isGrouped
		? get(listModel, MODEL_PATH.LAYOUT_QUERY.GROUP_COUNT, 0)
		: rowCount;
	const query = stripTextQuery(get(parsedQueryModel, 'queryString', ''));

	const gridProps = {
		allRecordsSelected,
		ariaTitle,
		customCellRenderer,
		columnWidths,
		columnWidthsResetRequested,
		columnsResized,
		highlightContent,
		isRefList,
		limit,
		gridModel: listModel,
		listInstanceId: instanceId,
		listTitle,
		liveListUpdates,
		loading,
		maxCharLimit,
		orderBy,
		page,
		parsedQueryModel,
		quickEditSysId,
		scrollHandlerThrottle,
		exceptedRecords,
		selectionCount,
		selectedRecords,
		table,
		wordWrap,
		hideCellFilter,
		hideColumnSorting,
		hideQuickEdit,
		hideColumnGrouping,
		hideColumnFiltering,
		hideColumnResizing,
		hideLinks,
		hideLiveList,
		hideHighlightedValues,
		hideShiftRecordSelection,
		hideRowSelector: shouldHideRowSelectors,
		hideCheckboxHover,
		hideSelectAll,
		hideViewAll,
		hideEmptyStateImage
	};

	const paginationProps = {
		hideRange,
		hidePages,
		hideRowCount,
		hideFirstPage,
		hidePreviousPage,
		hideNextPage,
		hideLastPage,
		hideLimitSelector,
		...getPaginationModel(state.properties)
	};

	const headerTitleProps = memoziedHeaderTitleProps({
		dataUpdatedTime,
		directLink,
		dispatch,
		encodedQueryString: get(listModel, 'encodedQueryString', ''),
		instanceId: instanceId,
		isRefList: false,
		metadata: get(listModel, 'tableMetadata', {}),
		parentTable,
		table,
		timeAccessed
	});

	const toolbarProps = {
		columns,
		isWorkspace,
		hideFilterPanel,
		hideMenuButton,
		hidePanel,
		hideColumnResizing,
		hideRefreshButton,
		hideListSharing,
		hideLiveList,
		isTableEmpty:
			!get(listModel, 'columns', []).length &&
			!get(listModel, 'data', []).length,
		listTitle,
		listType,
		liveListCount,
		liveLists,
		menuSelection,
		panelOpened,
		panelConfig,
		parsedQueryCount: parsedQueryModel.count,
		query,
		recordCount,
		relatedListName,
		selectedListId,
		size: headerSize,
		table,
		content: get(cascadeDelete, 'cascadeContent', '')
	};

	// sysIds will become unnecessary when DA only uses queries
	const sysIds = selectedRecords.toString();

	const declarativeActionsModel = memoizedDAProps({
		actionConfigId,
		allRecordsSelected,
		columns,
		count: selectionCount,
		isParentReadOnly,
		isParentNewRecord,
		label: listTitle,
		limit,
		listTitle,
		listType,
		maxColumns,
		modelType: 'LIST',
		page,
		parentRecordSysId,
		parentTable,
		query,
		relatedListName,
		selectionQuery,
		sysIds,
		table,
		view,
		workspaceConfigId
	});

	const hasPanel = !hidePanel && panelOpened;
	const headerClass = 'sn-list-header sn-list-header-' + headerSize;
	const omitCount = get(listModel, IS_OMIT_COUNT, false);

	const {
		panelTitle,
		panelSize,
		panelSlot,
		renderPanelHeader,
		hidePadding
	} = panelConfig;

	const finalCount = get(listModel, FINAL_COUNT, false);

	const listModalProps = {
		columns,
		isWorkspace,
		listTitle,
		...modalProps,
		originalConditions,
		query,
		selectedListId,
		table
	};
	const hideModal = !get(listModalProps, 'type', '');
	const viewAllParams = {
		listTitle: listTitle || table || '',
		table,
		query,
		columns,
		workspaceConfigId,
		disableQuickEdit: true
	};

	return (
		<div className="sn-list-container">
			{!hideModal ? <sn-record-list-modal modalProps={listModalProps} /> : null}
			{!hideHeader ? (
				<div className={headerClass}>
					{menuSelection ? (
						<sn-record-list-header-button-slide-menu
							menu-opened={listMenuOpen}
						/>
					) : null}
					<sn-record-list-header-title
						ariaTitle={ariaTitle}
						headerTitleProps={headerTitleProps}
						listTitle={listTitle}
						count={rowCount}
						hideTitleRowCount={hideTitleRowCount}
						hideTitle={hideTitle}
						hideLastRefreshedText={hideLastRefreshedText}
						headerSize={headerSize}
						headingLevel={headingLevel}
						omitCount={omitCount}
						finalCount={finalCount}
						liveLists={liveLists}
					/>
					<sn-record-list-header-toolbar {...toolbarProps} />
					{!hideDeclarativeActions ? (
						<sn-declarative-mediator should-wrap-action={!isWorkspace}>
							<sn-record-list-declarative-actions-wrapper
								actions={declarativeActions}
								count={selectionCount}
								model={declarativeActionsModel}
								headerSize={headerSize}
								headingLevel={headingLevel}
								isWorkspace={isWorkspace}
							/>
						</sn-declarative-mediator>
					) : null}
				</div>
			) : null}
			<div className="sn-list-grid-container">
				<div className="sn-list">
					{isGrouped ? (
						<now-treegrid {...gridProps} />
					) : useNowGrid ? (
						<div style={{height: '100%'}}>
							<now-grid
								columns={selectors.getColDefsFromBehavior(state)}
								rowData={selectors.getDataFromBehavior(state)}
								templates={templates}
								tableLayout={get(
									state,
									'behaviors.colResizing.tableStyleType.style.tableLayout',
									'auto'
								)}
								styles={{
									container: {style: {height: '100%', maxHeight: '2000px'}},
									...pluginStyles
								}}
							/>
						</div>
					) : (
						<now-table {...gridProps} />
					)}
				</div>
				{hasPanel ? (
					<now-record-list-panel
						renderPanelHeader={!!renderPanelHeader}
						panelTitle={t(`${panelTitle}`)}
						panelOpened={panelOpened}
						panelSize={panelSize}
						hidePadding={hidePadding}>
						<div className="now-record-list-panel-body">{panelSlot}</div>
					</now-record-list-panel>
				) : null}
			</div>
			<div className="sn-list-footer-container">
				{isViewAllNeeded({hideViewAll, limit, recordCount}) ? (
					<div className="view-all-container">
						<a
							className="sn-text-link"
							href="javascript:void(0)"
							on-click={() => {
								dispatch(ITEM_SELECTED, {
									renderer_type: 'layout',
									params: viewAllParams
								});
								const {listTitle, table, query, columns} = viewAllParams;
								dispatch(LIST_VIEW_ALL_CLICKED, {
									listTitle,
									table,
									query,
									columns
								});
							}}>
							{t('View All')}
						</a>
					</div>
				) : null}
				{!hidePagination ? (
					<div className="pagination-container">
						<now-pagination {...paginationProps} />
					</div>
				) : null}
			</div>
		</div>
	);
};

export default view;
