import {actionHandlers} from './actions';
import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

const view = state => {
	const {
		properties: {selectedRecords, table, workspaceConfigId}
	} = state;

	return (
		<sn-record-common-multi-form
			table={table}
			sys-ids={selectedRecords.join(',')}
			view={'workspace'}
			workspaceConfigId={workspaceConfigId}
		/>
	);
};

createCustomElement('sn-record-list-multi-edit', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		selectedRecords: {default: []},
		table: {default: ''},
		workspaceConfigId: {default: ''}
	},
	actionHandlers
});
