import {COMPONENT_PROPERTY_CHANGED} from '@servicenow/ui-core/lib/constants';
import {DEFAULT_SELECTOR, NOW_GRID_DOM_READY} from '../constants';
import {querySelector} from '@tectonic/seismic-element-helpers';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

export const PLUGIN_NAME = 'overlay';

const gridRefWeakMap = new WeakMap();

const getGridElement = (host, selector = DEFAULT_SELECTOR) => {
	return querySelector(selector, host);
};

const addOrRemoveLoadingState = ({host, loading}) => {
	const gridElementRef = getGridElement(host);

	if (gridRefWeakMap.has(gridElementRef)) {
		const {loadingContainer} = gridRefWeakMap.get(gridElementRef);
		if (loading) {
			loadingContainer.classList.remove('sr-only');
		} else if (gridElementRef.contains(loadingContainer)) {
			loadingContainer.classList.add('sr-only');
		}
	}
};

const addOrRemoveEmptyState = ({host, listModel}) => {
	const gridElementRef = getGridElement(host);

	if (gridRefWeakMap.has(gridElementRef)) {
		const {emptyStateContainer} = gridRefWeakMap.get(gridElementRef);
		const isModelEmpty = isEmpty(get(listModel, 'layoutQuery.queryRows', {}));

		if (isModelEmpty) {
			emptyStateContainer.classList.remove('sr-only');
		} else if (gridElementRef.contains(emptyStateContainer)) {
			emptyStateContainer.classList.add('sr-only');
		}
	}
};

const attachLoaderAndEmptyState = ({host, hideEmptyStateImage, listModel}) => {
	const gridElementRef = getGridElement(host);
	if (
		!gridElementRef ||
		gridElementRef.querySelector('sn-record-list-state-empty') !== null
	)
		return;

	const loadingContainer = document.createElement('now-grid-overlay-loader');
	loadingContainer.classList.add('sr-only');
	gridElementRef.appendChild(loadingContainer);

	const emptyStateContainer = document.createElement(
		'sn-record-list-state-empty'
	);
	const isModelEmpty = isEmpty(get(listModel, 'layoutQuery.queryRows', {}));

	if (!isModelEmpty) emptyStateContainer.classList.add('sr-only');

	emptyStateContainer.hideEmptyStateImage = hideEmptyStateImage;
	gridElementRef.appendChild(emptyStateContainer);

	gridRefWeakMap.set(gridElementRef, {
		loadingContainer,
		emptyStateContainer
	});
};

const handleEmptyStateChangeFlag = ({host, value}) => {
	const gridElementRef = getGridElement(host);
	if (!gridElementRef) return;
	const snRecordEmpty = gridElementRef.querySelector(
		'sn-record-list-state-empty'
	);
	if (snRecordEmpty) {
		snRecordEmpty.hideEmptyStateImage = value;
	}
};

export function instantiatePlugin(options) {
	return {
		behavior: {
			name: PLUGIN_NAME,
			initialState: {},
			actionHandlers: {
				[NOW_GRID_DOM_READY]: {
					effect: coeffects => {
						const {
							host,
							properties: {hideEmptyStateImage, listModel}
						} = coeffects;
						attachLoaderAndEmptyState({
							host,
							hideEmptyStateImage,
							listModel
						});
					}
				},
				[COMPONENT_PROPERTY_CHANGED]: {
					effect: coeffects => {
						const {
							action: {
								payload: {name, value}
							},
							host
						} = coeffects;

						if (name === 'loading') {
							addOrRemoveLoadingState({host, loading: value});
						} else if (name === 'listModel') {
							addOrRemoveEmptyState({host, listModel: value});
						} else if (name === 'hideEmptyStateImage') {
							handleEmptyStateChangeFlag({host, value});
						}
					}
				}
			}
		}
	};
}
