import {createCustomElement} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './emptyContent.scss';

const view = ({properties}) => {
	const {hideEmptyStateImage} = properties;
	return (
		<div className="sn-empty-state">
			{!hideEmptyStateImage ? (
				<div className="sn-empty-state-row -behind">
					<div className="sn-empty-state-img -no-records" />
				</div>
			) : null}
			<div className="sn-empty-state-row -header">
				{t('No records to display.')}
			</div>
		</div>
	);
};

createCustomElement('sn-record-list-state-empty', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		hideEmptyStateImage: {}
	},
	styles
});
