import '@servicenow/now-split-button';
import {COUNT_TEMPLATE, DECLARATIVE_ACTIONS} from '../constants';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {Logger} from '../../../utils/logger';
import {getButtonAttributes} from '../helpers';
import isEmpty from 'lodash/isEmpty';
import slice from 'lodash/slice';

const LOG = Logger().createLog('DeclarativeActionsSplitButton');

const DeclarativeActionsSplitButton = ({actions, count, model}) => {
	if (isEmpty(actions)) return null;

	const mainAction = actions[0];
	const button = getButtonAttributes(mainAction, count);

	LOG.log('Actions/Model: ', actions, model);

	return (
		<Fragment>
			<now-split-button
				label={button.label}
				variant="secondary"
				size={DECLARATIVE_ACTIONS.BUTTON_SIZE}
				items={slice(actions, 1).map(item => {
					return {
						id: item.name,
						label: item.label.replace(COUNT_TEMPLATE, count)
					};
				})}
				append-to-payload={{actions, model}}
				component-name={DECLARATIVE_ACTIONS.SPLIT_BUTTON}
				{...button.disabled}
			/>
		</Fragment>
	);
};

export default DeclarativeActionsSplitButton;
