import * as clipboard from 'clipboard-polyfill';
import {createCustomElement} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {Logger} from '../../utils/logger';
import {RECORD_LIST_NOTIFICATION_ADDED} from '../../constants';

const LOG = Logger().createLog('listCopyUrlButton');

const notify = dispatch => {
	dispatch(RECORD_LIST_NOTIFICATION_ADDED, {
		alertList: [
			{
				id: `now_alert_positive`,
				status: 'positive',
				content: {
					type: 'string',
					value: t('URL copied to clipboard')
				},
				action: {type: 'dismiss'}
			}
		]
	});
};

const view = ({properties}, {dispatch}) => {
	const {size, variant, listTitle} = properties;
	const ariaLabel = `${t('Copy Url for')} ${listTitle}`;
	const ariaConfig = {'aria-label': ariaLabel};

	return (
		<div>
			<now-button
				tooltip-content={ariaLabel}
				config-aria={ariaConfig}
				icon-name="square-share-outline"
				size={size}
				variant={variant}
				on-click={() => {
					const url = window.location.href;
					clipboard.writeText(url).then(
						() => notify(dispatch, url),
						() => {
							LOG.log('Error ocurred when trying to copy URL to clipboard');
						}
					);
				}}
			/>
		</div>
	);
};

createCustomElement('sn-record-list-header-toolbar-button-copy-url', {
	properties: {
		size: {default: 'md'},
		variant: {default: 'secondary'},
		listTitle: {default: ''}
	},
	renderer: {
		type: snabbdom,
		view
	}
});
