import {BUTTON_CLICKED} from '../../../constants';
import {dirtyModalInterceptor} from '../../../behaviors/dirtyBehavior/dirtyBehavior';
import {dispatchToMediator} from '../helpers';

export default {
	[BUTTON_CLICKED]: {
		effect: ({dispatch, action: {payload}}) => {
			const {action, model} = payload;
			dispatchToMediator({action}, {model}, dispatch);
		},
		stopPropagation: true,
		interceptors: [dirtyModalInterceptor]
	}
};
